<script lang="coffeescript">

  export disabled = undefined
  export state = "primary"
  export style = undefined
  export classes = undefined
  export id = undefined
</script>

<button type="button" {disabled} {id} {state} {style} class={classes} on:click><slot/></button>


<style lang="sass">

  // standard

  // div is for fake buttons
  button
    font-family: var(--font-family-primary), -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"
    
    width: auto
    padding: 6px 12px
    background-color: var(--lighter-gray)
    color: var(--black)
    border-radius: 4px

    &:hover
      background-color: var(--lightest-gray)
      cursor: pointer
    &:active, &:focus
      background-color: var(--gray)
    &[disabled], &[disabled]:hover, &[disabled]:focus, &[disabled]:active
      background: var(--lightest-gray)
      color: var(--light-gray)

    &[critical], &[state="critical"]
      background-color: var(--critical)
      color: var(--white)
      &:hover
        background-color: var(--critical-hover)


      &:active, &:focus
        background-color: var(--critical-active)
      &[disabled], &[disabled]:hover, &[disabled]:focus, &[disabled]:active
        background-color: var(--critical-disabled)
        color: var(--dark-gray)
    &[primary], &[state="primary"]
      background-color: var(--primary)
      color: var(--white)
      &:hover
        background-color: var(--primary-hover)
      &:active, &:focus
        background-color: var(--primary-active)
      &[disabled], &[disabled]:hover, &[disabled]:focus, &[disabled]:active
        background-color: var(--primary-disabled)
        color: var(--dark-gray)



</style>