<script lang="coffeescript">


  import { onMount } from "svelte"

  import FileSaver from "file-saver"
  import md5 from "md5"


  # Data Items

  import { 
    debug,
    Button,
    FileInput
    InlineInput,
    InlinePassword,
    Checkbox 
  } from "../../Braintapper"

  import { 
    
    List
    ListItem
    PageContent
    PageHeader 
  } from "../../Application"



  


  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()




  # resource = new RootItemList()

  replicationEnabled = localStorage.getItem("radar_sync")
  replicationDb = localStorage.getItem("radar_remote_db")
  replicationRequiresAuthentication = localStorage.getItem("sync_authentication") || "false"


  toggleSync = ()->
    localStorage.setItem "radar_sync"
    !(replicationEnabled == 'true')
    replicationEnabled = localStorage.getItem("radar_sync")


  importData = ()->
    console.error "importData"
    console.debug files
    if files?
      console.log "file info"
      fileArray = Array.from(files)
      console.debug fileArray.first()
      reader = new FileReader()
      reader.onloadend = (response)->
        console.log "READ FILE"
        console.log response
        jsonFile  = response.target.result

        jsonToData = JSON.parse(jsonFile).map (o)->
          delete o._rev
          return o


        jsonToData.forEach (item)->
          #item.id = uuid()
          item["_id"] = item.id
          # item.name = "#{item.name} imported at #{Date.create()}"
        
        db.bulkDocs(jsonToData)
        .then (result)->
          console.error "imported"
          console.debug result


      reader.readAsText fileArray.first()

  updateDb = (e)->
    console.log e

  updateUser = (e)->
    console.log e

  updatePassword = (e)->
    console.log e





  forceSync = ()->
    console.log remoteDb
    if remoteDb?
      console.log "syncing to remote db"
      db.sync( remoteDb, {retry: true} )
      .on 'complete', ()->
        console.log "complete!"
      .on 'change', (change) ->
        # yo, something changed!
        console.log "sync change"
        console.log change
      .on 'paused', (info) ->
        # replication was paused, usually because of a lost connection
        console.log "sync paused"
        console.log info
      .on 'active', (info) ->
        # replication was resumed
        console.log "sync active"
        console.log info
      .on 'error', (err) ->
        console.log "sync error"
        console.log err




  notificationPermssionRequest = ()->
    Notification.requestPermission().then (permission) ->
      # If the user accepts, let''s create a notification
      if permission == 'granted'
        notification = new Notification('Hi there!')
      else
        console.log "not granted"
      return

  if electron
    edition = "Desktop"
  else
    edition = "Web"
  `$: disableReplication = (replicationEnabled != 'true')`


  # resource
  cleanLists = ()->
    db.find({selector: {doctype: "List"}}).then (result)->
      db.bulkDocs result.docs.map (doc)->
        doc["_deleted"] = true
        return doc
      .then  (result)->
        console.error "Lists deleted"

  cleanListItems = ()->
    db.find({selector: {doctype: "ListItem"}}).then (result)->
      db.bulkDocs result.docs.map (doc)->
        doc["_deleted"] = true
        return doc
      .then  (result)->
        console.error "cleanListItems deleted"

  compact = ()->
    db.compact()
    .then (result)->
      console.log "COMPACTED"
      console.log result

  migrate = ()->
    console.error "start migration"
    staging = []
    output = []
    migrateScb = (docs)->
      
      docs.forEach (doc)->
        console.log "doc: #{doc.name}, list_items: #{doc.list_items.length}"
        
        # doc.migrate(staging)


      console.table staging

      
      staging.forEach (item)->

        if item.type == "List"
          newModel = new List(item)
          
        else
          newModel = new ListItem(item)
          

        output.append newModel.pouchify()



      jsonString = JSON.stringify(output,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})
      # note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
      # use a different handler for electron
      FileSaver.saveAs blob, "braintapper-radar-migrated-#{Date.create().toTimestampId()}--#{md5(jsonString)}.json"


      
      db.bulkDocs(output)
      .then (result)->
        console.error "imported new lists and list items"
        console.debug result

    resource.selectAll migrateScb



  #//              __  __                   _   
  #//   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
  #//  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
  #// | (_) | | | | |  | | (_) | |_| | | | | |_ 
  #//  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
                                             

  onMount ()->
    

    #settings.subscribe  (val)->
    #  console.error "Settings.Index on mount setting changed"
    #  console.log val

</script>

<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <PageHeader><h1 title>Settings</h1></PageHeader>
  <article type="content">

    <h2></h2>


  </article>

</PageContent>
<style lang="sass">
  article
    margin: 0px

</style>
