var PouchDoc;

PouchDoc = (function() {
  class PouchDoc {
    // requires window.db to exist and be used for Pouch

    ////   ____ ___  _   _ ____ _____ ____  _   _  ____ _____ ___  ____  
    ////  / ___/ _ \| \ | / ___|_   _|  _ \| | | |/ ___|_   _/ _ \|  _ \ 
    //// | |  | | | |  \| \___ \ | | | |_) | | | | |     | || | | | |_) |
    //// | |__| |_| | |\  |___) || | |  _ <| |_| | |___  | || |_| |  _ < 
    ////  \____\___/|_| \_|____/ |_| |_| \_\\___/ \____| |_| \___/|_| \_\
    ////                                                                 
    constructor(model) {
      
      //console.log "BEFORE"
      //console.log @
      this.after_construct(model);
    }

    //console.log "COSNTRUCTOR"
    after_construct(model) {}

    
    ////      _       __             _ _                _                 __            
    ////   __| | ___ / _| __ _ _   _| | |_  __   ____ _| |_   _  ___     / _| ___  _ __ 
    ////  / _` |/ _ \ |_ / _` | | | | | __| \ \ / / _` | | | | |/ _ \   | |_ / _ \| '__|
    //// | (_| |  __/  _| (_| | |_| | | |_   \ V / (_| | | |_| |  __/   |  _| (_) | |   
    ////  \__,_|\___|_|  \__,_|\__,_|_|\__|___\_/ \__,_|_|\__,_|\___|___|_|  \___/|_|   
    ////                                 |_____|                   |_____|              
    default_value_for(key) {
      var output;
      output = null;
      switch (key) {
        case "id":
        case "_id":
          output = uuid();
          break;
        default:
          output = null;
      }
      return output;
    }

    ////            _                 _                    _                 __            
    ////  _ __ ___ (_) __ _ _ __ __ _| |_ ___  __   ____ _| |_   _  ___     / _| ___  _ __ 
    //// | '_ ` _ \| |/ _` | '__/ _` | __/ _ \ \ \ / / _` | | | | |/ _ \   | |_ / _ \| '__|
    //// | | | | | | | (_| | | | (_| | ||  __/  \ V / (_| | | |_| |  __/   |  _| (_) | |   
    //// |_| |_| |_|_|\__, |_|  \__,_|\__\___|___\_/ \__,_|_|\__,_|\___|___|_|  \___/|_|   
    ////              |___/                 |_____|                   |_____|              

    // Override value for an attribute - use this to migrate old or invalid data
    migrate_value_for(model, key) {
      var output;
      output = model[key];
      return output;
    }

    
    ////  _                          _                   _             
    //// | |__   ___  _   _ ___  ___| | _____  ___ _ __ (_)_ __   __ _ 
    //// | '_ \ / _ \| | | / __|/ _ \ |/ / _ \/ _ \ '_ \| | '_ \ / _` |
    //// | | | | (_) | |_| \__ \  __/   <  __/  __/ |_) | | | | | (_| |
    //// |_| |_|\___/ \__,_|___/\___|_|\_\___|\___| .__/|_|_| |_|\__, |
    ////                                          |_|            |___/ 

    // override if any attributes from the database need deleting, etc.
    housekeeping(model) {
      return Object.select(model, this.attribute_whitelist);
    }

    ////             __                            _      _ 
    ////  ___  __ _ / _| ___   _ __ ___   ___   __| | ___| |
    //// / __|/ _` | |_ / _ \ | '_ ` _ \ / _ \ / _` |/ _ \ |
    //// \__ \ (_| |  _|  __/ | | | | | | (_) | (_| |  __/ |
    //// |___/\__,_|_|  \___| |_| |_| |_|\___/ \__,_|\___|_|

    // override if object contains classes, etc.
    safe_model() {
      return Object.select(this, this.attribute_whitelist);
    }

    ////      _         _     
    ////  ___| |_ _   _| |__  
    //// / __| __| | | | '_ \ 
    //// \__ \ |_| |_| | |_) |
    //// |___/\__|\__,_|_.__/ 

    // Create a new item with default values
    stub() {
      var output, that;
      that = this;
      output = {};
      that.attribute_whitelist.forEach(function(key) {
        return output[key] = that.default_value_for(key);
      });
      output["_id"] = that.id;
      return output;
    }

    ////                       
    ////  ___ _   _ _ __   ___ 
    //// / __| | | | '_ \ / __|
    //// \__ \ |_| | | | | (__ 
    //// |___/\__, |_| |_|\___|
    ////      |___/            
    sync() {
      //console.log "pouchresourece.sync"
      return window.pouchSync();
    }

    ////      _                             _     _  __       
    ////   __| | ___ _ __   ___  _   _  ___| |__ (_)/ _|_   _ 
    ////  / _` |/ _ \ '_ \ / _ \| | | |/ __| '_ \| | |_| | | |
    //// | (_| |  __/ |_) | (_) | |_| | (__| | | | |  _| |_| |
    ////  \__,_|\___| .__/ \___/ \__,_|\___|_| |_|_|_|  \__, |
    ////            |_|                                 |___/ 

    // Convert a PouchDb item into this class
    depouchify(model) {
      var that;
      // get the document, map the attributes to this class instance
      // recurse through the list_items attributes and convert
      // the list_items to ListItem classes
      // console.log "depouchify"
      that = this;
      model = this.housekeeping(model);
      return this.attribute_whitelist.forEach(function(key) {
        // check that model has key
        if (Object.has(model, key)) {
          return that[key] = that.migrate_value_for(model, key);
        } else {
          return that[key] = that.default_value_for(model, key);
        }
      });
    }

    ////                         _     _  __               
    ////  _ __   ___  _   _  ___| |__ (_)/ _|_   _         
    //// | '_ \ / _ \| | | |/ __| '_ \| | |_| | | |        
    //// | |_) | (_) | |_| | (__| | | | |  _| |_| |        
    //// | .__/ \___/ \__,_|\___|_| |_|_|_|  \__, |        
    //// |_|                                 |___/         

    // Prepare this model to be saved into PouchDB
    pouchify() {
      var that, writeable;
      that = this;
      writeable = {};
      if (that.id == null) {
        that.id = uuid();
      }
      that.attribute_whitelist.forEach(function(key) {
        if (that[key] != null) {
          return writeable[key] = that[key];
        } else {
          return writeable[key] = that.default_value_for(key);
        }
      });
      writeable["_id"] = that.id;
      return writeable;
    }

    ////  ____    _  _____  _    ____    _    ____  _____    ____ ____  _   _ ____     
    //// |  _ \  / \|_   _|/ \  | __ )  / \  / ___|| ____|  / ___|  _ \| | | |  _ \    
    //// | | | |/ _ \ | | / _ \ |  _ \ / _ \ \___ \|  _|   | |   | |_) | | | | | | |   
    //// | |_| / ___ \| |/ ___ \| |_) / ___ \ ___) | |___  | |___|  _ <| |_| | |_| |   
    //// |____/_/   \_\_/_/   \_\____/_/   \_\____/|_____|  \____|_| \_\\___/|____/    
    select(selector, thenCb, catchCb, finallyCb) {
      return db.find(selector).then(function(result) {
        if (thenCb != null) {
          // consider removing transformation
          return thenCb(result.docs);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("PouchDoc.select");
          return console.debug(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    patch(change, thenCb) {
      var that;
      that = this;
      Object.keys(change).forEach(function(key) {
        return that[key] = change[key];
      });
      change["id"] = this.id;
      return db.get(this.id).then(function(originalDoc) {
        Object.merge(originalDoc, change);
        // force cleanup of deprecated attributes
        originalDoc = that.housekeeping(originalDoc);
        originalDoc.updated_at = Date.create();
        return db.put(originalDoc).then(function(response) {
          console.log("update response");
          console.log(response);
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          } else {
            console.log("patch.catch");
            return console.log(err);
          }
        }).finally(function() {
          // that.sync()
          if (typeof finallyCb !== "undefined" && finallyCb !== null) {
            return finallyCb();
          }
        });
      });
    }

    bulkPatch(changedDocs, thenCb, catchCb, finallyCb) {}

    update(thenCb, catchCb, finallyCb) {
      var changedDoc, that;
      that = this;
      changedDoc = this.pouchify();
      return db.get(changedDoc.id).then(function(originalDoc) {
        changedDoc["_id"] = changedDoc.id;
        changedDoc["_rev"] = originalDoc._rev;
        // Object.merge
        changedDoc.updated_at = Date.create();
        return db.put(changedDoc).then(function(response) {
          console.log("--------------root item updated");
          if (thenCb != null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("List.update");
          return console.debug(err);
        }
      }).finally(function() {
        window.pouchSync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          "id": id
        }
      }).then(function(response) {
        var refreshedCb, removedCb;
        if (thenCb != null) {
          refreshedCb = function() { // execute then cb
            return thenCb(that.items);
          };
          removedCb = function() { // refresh @ items
            // that.sync()
            // that.select refreshedCb, catchCb, finallyCb
            if (thenCb) {
              return thenCb();
            }
          };
          console.log("response");
          console.log(response);
          // docToRemove = response.docs.first()
          response.docs.forEach(function(doc) {
            return doc["_deleted"] = true;
          });
          
          // db.remove docToRemove, removedCb
          return db.bulkDocs(response.docs).then(removedCb).catch(function(err) {
            if (catchCb != null) {
              return catchCb(err);
            } else {
              console.error("List.destroy");
              return console.debug(err);
            }
          });
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.error("List.destroy");
          return console.debug(err);
        }
      }).finally(function() {
        // that.sync()
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  // console.log "POUCHDOC AFTERCONSTRUCT"

  ////  __  __  ___  ____  _____ _     
  //// |  \/  |/ _ \|  _ \| ____| |    
  //// | |\/| | | | | | | |  _| | |    
  //// | |  | | |_| | |_| | |___| |___ 
  //// |_|  |_|\___/|____/|_____|_____|
  ////                                 
  PouchDoc.prototype.id = null;

  PouchDoc.prototype.doctype = "PouchDoc";

  PouchDoc.prototype.attribute_whitelist = ["id", "model_version", "doctype", "name", "created_at", "updated_at"];

  return PouchDoc;

}).call(this);

module.exports = PouchDoc;
