<script lang="coffeescript">

  import { onMount } from 'svelte'
  import Router from 'svelte-spa-router'
  

  import {  
    debug
    CssColors 
    CssFonts 
    CssResets 
    # CssResponsive
    CssTypography
    CssDarkMode
    Toast
    LayoutLeftSplit
    FavIconBadge 
  } from './Braintapper'
  
  import { 
    leftSplitContentElement
    SideNavigator
    CssGlobal
    PouchInit 
  } from "./Application"

  
  window.debug = true

  debug.on = window.debug


 


  # Routes

  import Radar from './Routes/Radar/Index.svelte'
  import Lists from './Routes/Lists/Index.svelte'
  import Favorites from './Routes/Favorites/Index.svelte'
  import Archived from './Routes/Archived/Index.svelte'
  import List from './Routes/Lists/Show.svelte'
  import Search from './Routes/Search/Index.svelte'
  import Tools from './Routes/Tools/Index.svelte'
  import Settings from './Routes/Settings/Index.svelte'
  import Sync from './Routes/Sync/Index.svelte'
  import Help from './Routes/Help/Index.svelte'
  import FourOFour from './Routes/FourOFour.svelte'


  showToast = false
  toastMessage = ""

  routes =
    '/': Radar
    '/lists': Lists
    '/favorites': Favorites
    '/archived': Archived
    '/lists/:id': List
    '/settings': Settings
    '/search': Search
    '/search/:terms': Search
    '/tools/': Tools
    '/sync': Sync
    '/help': Help
    '*': FourOFour


  appName = "Braintapper Radar Lists"


  currentRoute = ""


  contentEl  = undefined

  

  onMount ()->
    leftSplitContentElement.set contentEl
    window.mounted()

  #//  ____             _         _____                 _          
  #// |  _ \ ___  _   _| |_ ___  | ____|_   _____ _ __ | |_ ___    
  #// | |_) / _ \| | | | __/ _ \ |  _| \ \ / / _ \ '_ \| __/ __|   
  #// |  _ < (_) | |_| | ||  __/ | |___ \ V /  __/ | | | |_\__ \   
  #// |_| \_\___/ \__,_|\__\___| |_____| \_/ \___|_| |_|\__|___/   
                                                                

  routeLoaded = (e)->
    console.error "routeLoaded"
    console.log e.detail
    currentRoute = e.detail
  

  routeEvent = (e)-> #  router must be mounted or won't work
    console.error "routeEvent"
    console.debug e

  toaster = (e)->


  syncChange = (response)->
    console.log "App.svelte::syncChange"
    console.log response
  syncPaused = (response)->
    console.log "App.svelte::syncPaused"

  syncActive = ()->
    console.log "App.svelte::syncActive"

  syncDenied = (response)->
    console.log "App.svelte::syncDenied"
    console.log response
  syncComplete = (response)->
    console.log "App.svelte::syncComplete"
    console.log response
  syncError = (response)->
    console.log "App.svelte::syncError"
    console.log response
    
  `$: show = showToast`

</script>


<svelte:head>
  <title>{ appName }</title>
</svelte:head>

<PouchInit 
  database="braintapper-radar"
  on:syncChange
  on:syncPaused
  on:syncActive
  on:syncDenied
  on:syncComplete
  on:syncError
  
  >
</PouchInit>



<LayoutLeftSplit contentScrollable={true} on:toast={toaster}>
  <div slot="nav">
    <SideNavigator on:toast={toaster}/>
  </div>
  <div slot="default" style="padding: 48px 96px" bind:this={contentEl}>
    <Router {routes} on:routeEvent={routeEvent} on:routeLoaded={routeLoaded}/>
  </div>
</LayoutLeftSplit>
<Toast bind:show={showToast} message={toastMessage} delay={2}/>



