/*

  %s  Formats the value as a string
  %i or %d  Formats the value as an integer
  %f  Formats the value as a floating point value
  %o  Formats the value as an expandable DOM element. As seen in the Elements panel
  %O  Formats the value as an expandable JavaScript object
  %c  Applies CSS style rules to the output string as specified by the second parameter

*/
  /*
    console.group
    console.cirss
  */
var debug;

debug = {
  on: false,
  timestamps: true,
  timestamp_format: "{HH}:{mm}:{ss}.{SSS}",
  context: null,
  emoji: function(shortname) {
    // https://apps.timwhitlock.info/emoji/tables/unicode
    // https://unicode.org/emoji/charts/full-emoji-list.html
    // https://www.w3schools.com/charsets/ref_emoji.asp
    // https://www.w3schools.com/charsets/ref_utf_symbols.asp
    switch (shortname) {
      case "error":
        return String.fromCodePoint(0x1F525);
      case "warn":
        return String.fromCodePoint(0x1F7E0); // 0x26A0
      case "code":
        return String.fromCodePoint(0x1F4DC);
      case "flag":
        return String.fromCodePoint(0x1F6A9);
      case "wrench":
        return String.fromCodePoint(0x1F527);
      case "gear":
        return String.fromCodePoint(0x2699);
      case "printer":
        return String.fromCodePoint(0x1F5A8); // info
      default:
        return String.fromCodePoint(0x1F7E9);
    }
  },
  _normal: "color: black",
  _neutral: "color: gray; background: blue; width: 100%",
  _border_test: "border-bottom: 1px solid black; display: block; width: 100%;",
  _timestamp: "color: green",
  _green: "color: green",
  _red: "color: red",
  _yellow: "color: orange",
  banner: function(banner) {
    if (banner != null) {
      return console.log(`${"-".repeat(80) //, @_border_test
}`);
    }
  },
  //console.log ""
  //console.log banner
  //console.log ""
  //console.log "#{"-".repeat(80)}" #, @_border_test
  context_string: function() {
    if (this.context != null) {
      return `%c${this.context} :: `;
    } else {
      return "";
    }
  },
  log: function(message, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji()} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._green, this._normal);
      return this.banner(banner);
    }
  },
  checkpoint: function(message, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji('flag')} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._green, this._normal);
      return this.banner(banner);
    }
  },
  warn: function(message, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji('warn')} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._yellow, this._normal);
      return this.banner(banner);
    }
  },
  error: function(message, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji('error')} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._red, this._normal);
      return this.banner(banner);
    }
  },
  object: function(message, obj, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji('gear')} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._green, this._normal);
      this.banner(banner);
      console.debug(obj);
      return this.banner(banner);
    }
  },
  table: function(message, obj, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji('printer')} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._green, this._normal);
      this.banner(banner);
      console.table(obj);
      return this.banner(banner);
    }
  },
  json: function(message, obj, banner) {
    if (this.on) {
      this.banner(banner);
      console.log(`${this.emoji("code")} %c${Date.create().format(this.timestamp_format)} :: ${this.context_string()}%c${message}`, this._green, this._normal);
      this.banner(banner);
      console.table(obj);
      return this.banner(banner);
    }
  }
};

module.exports = debug;
