<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"

  dispatch = createEventDispatcher()


  import { 
    IconSvg
    MenuContainer
    MenuItemWithIcon
    MenuDivider
  } from "../../../Braintapper"
  

  select = (value)->

    dispatch "tippy-event", { event: "select", value: value }

</script>

<MenuContainer>
  <MenuItemWithIcon on:click={select("add")}>
    <div slot="icon"><IconSvg icon="add"/></div>
    Add New Item Here
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("add-clipboard")}>
    <div slot="icon"><IconSvg icon="add"/></div>
    Add From Clipboard
  </MenuItemWithIcon>
</MenuContainer>
