<script lang="coffeescript">


  import { fade, fly, slide, scale, draw } from "svelte/transition"

  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { ButtonIcon,  IconSvg, IconToggleSvg } from "../../Braintapper"

  import { createEventDispatcher, onMount } from "svelte"
  import { sineInOut } from "svelte/easing"


  export style = undefined
  export checked = false
  export label = undefined
  export key = undefined

  dispatch = createEventDispatcher()

  duration = 250

  toggle = (e)->
    checked = !checked
    dispatch "change", {label: label, key: key, checked: checked}

</script>



<FlexRow>
  <FlexRowCell flex="initial" style="line-height: 24px; padding-right: 8px">
    <ButtonIcon on:click={toggle} {style}>
        <IconToggleSvg value={checked} whenTrue="checked" whenFalse="unchecked"/>           
        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" >
    </ButtonIcon>
  </FlexRowCell>
  <FlexRowCell flex style="line-height: 24px">
  {#if label.length > 0}
  <label on:click={toggle}>{label}</label>
  {/if}
  </FlexRowCell>
</FlexRow>
