<script lang="coffeescript">

  ###

    Note, for PouchDB to work, the rollup JS file must have

    * rollup-plugin-node-builtins
    * rollup-plugin-node-globals

    with globals() AFTER commonjs()

  ###

  import {createEventDispatcher, onMount } from "svelte"
  dispatch = createEventDispatcher()

  import PouchDB from 'pouchdb-browser'
  import PouchDBFind from 'pouchdb-find'

  # Add PouchDBFind plugin
  PouchDB.plugin PouchDBFind

  import SyncSettings from "../Models/SyncSettings.js"

  import {
    PouchSyncSettings
    PouchIsSyncing 
    PouchLastSyncResponse
  } from "./stores.js"
  

   #//                            
   #//  _ __  _ __ ___  _ __  ___ 
   #// | '_ \| '__/ _ \| '_ \/ __|
   #// | |_) | | | (_) | |_) \__ \
   #// | .__/|_|  \___/| .__/|___/
   #// |_|             |_|        

  export database = undefined # local database name from props, required
  export syncChange = undefined
  export syncPaused = undefined
  export syncActive = undefined
  export syncDenied = undefined
  export syncComplete = undefined
  export syncError = undefined


  pouchIsSyncing = false

  currentSyncSettings = undefined

  window.syncEnabled = false
  window.autoSync = false
  window.autoSyncInterval = 60000


  window.db = new PouchDB(database, {auto_compaction: true})
  window.db_remote = undefined

  syncSettings = new SyncSettings PouchSyncSettings

  window.pouchSync = ()->
    that = @
    # conditions
    console.error "#{Date.create().format("{HH}:{mm}:{ss}.{SSS}")} Sync Attempt"
    console.log currentSyncSettings

    unless window.db_remote?
      console.log "p[ouchsinc  window.dbremote :#{window.db_remote}"
      console.log "need to reinit"
      initRemoteDb()

    console.error "window.sync presync:"
    console.log window.db_remote

    window.db.sync( window.db_remote, {retry: true} )
    .on "change", (response)->
      # handle change
      console.log "InitPouchDB: Sync: sync changes"
      console.log response

      response["type"] = "change"
      response["handled"] = false

      PouchLastSyncResponse.set response
      # console.debug response.change.docs
      if syncChange?
        syncChange response


    .on "paused", (response)->
      # replication up to date, user went offline
      console.log "PouchDB: Sync: paused"
      console.log response
      if syncPaused?
        syncPaused response      
    .on "active", ()->
      # resumed - changes replicating, user went back online
      console.log "PouchDB: Sync: active"
      if syncActive?
        syncActive response            
    .on "denied", (response)->
      # failed to replicate
      console.log "PouchDB: Sync: sync denied"
      console.log response
      if syncDenied?
        syncDenied response               
    .on "complete", (response)->
      currentSyncSettings.last_successful_sync = Date.create()
      PouchSyncSettings.set currentSyncSettings      
      if console?
        console.log response          
    .on "error", (response)->
      console.error "PouchDB: Sync: error"
      if syncError?
        syncError response          

  window.db_remote = undefined

  initRemoteDb = ()->
    # unless window.db_remote?
    if mounted & currentSyncSettings?
      unless Object.isBoolean(currentSyncSettings)   # first sub hit, skip
        unless window.db_remote?
          console.error "initRemoteDb"
          console.log window.db_remote
          console.log currentSyncSettings
          remoteDbUrl = "https://#{currentSyncSettings.remote_database_user}:#{currentSyncSettings.remote_database_password}@#{currentSyncSettings.remote_database_host}:#{currentSyncSettings.remote_database_port}/#{currentSyncSettings.remote_database_name}"
          console.log "remoteDbUrl: #{remoteDbUrl}"
          # todo: do validations
          window.db_remote = new PouchDB(remoteDbUrl)
          console.log "window.db_remote"
          console.log window.db_remote
            


  window.mounted = ()->
  # todo: validate that this is still needed?
  #  window.pouch.sync_store = PouchIsSyncing
  #  window.pouch.notifier = toast
  #  window.pouchSync()


  syncTimer = undefined

  # object containing last settings and new settings
  lastSyncResult = undefined

  mounted = false

  onMount ()->
    mounted = true
    console.log JSON.stringify(syncSettings.pouchify(),null, 2)
    PouchSyncSettings.subscribe  (val)->
      console.error "INIT setting changed"
      console.log val
      lastSyncResult =
        previous: Object.clone(currentSyncSettings)
        new: Object.clone(val)

      currentSyncSettings = val
      
    PouchIsSyncing.subscribe (val)->
    syncCheck() # run on first load


  syncCheck = ()->
    doSync = true
    unless pouchIsSyncing
      console.log "syncCheck: sync now ok"
      console.log currentSyncSettings
      if currentSyncSettings.enabled # then sync
        sync = false
        
          
        if currentSyncSettings.last_sync_attempt?
          sync = true
        else
          console.log "sync interval: #{parseInt(currentSyncSettings.auto_sync_interval)}"
          console.log "minutes since last sync:  #{Date.create().minutesSince(Date.create(currentSyncSettings.last_sync_attempt))}"
          sync = Date.create().minutesSince(Date.create(currentSyncSettings.last_sync_attempt)) >= parseInt(currentSyncSettings.auto_sync_interval)
        if sync 
          unless window.db_remote?
            initRemoteDb()
          window.pouchSync()
          currentSyncSettings.last_sync_attempt = Date.create()
          PouchSyncSettings.set currentSyncSettings

    else
      console.log "Skipping sync because currently syncing"




  syncCheck.every(30000) # check every 30 seconds
  

  refreshSyncSettings = (obj)->
    console.error "refreshSyncSettings"
    if lastSyncResult?
      console.log "refreshSyncSettings: lastSyncResult"
      console.log obj
      unless window.db_remote?
        initRemoteDb()
    else  
      console.log "refreshSyncSettings: lastSyncResult not initialized, skipping..."





  `$: refreshSyncSettings(lastSyncResult)`

</script>