<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    debug,
    IconSvg,
    IconToggleSvg,
    MenuContainer,
    MenuDivider,
    MenuItemWithIcon
  } from "../../../Braintapper"


  export options = 
    is_hidden: undefined
    has_link: false
    due_date: false
    is_ongoing: false
    context: "list" # default

  select = (value)->
    dispatch "tippy-event", { event: "select", value: value }


</script>
<MenuContainer>
  {#if options.has_link}
  
  <MenuItemWithIcon on:click={select("openLink")}>
    <div slot="icon"><IconSvg icon="link"/></div>
    Open Link
  </MenuItemWithIcon>
  
  <MenuItemWithIcon on:click={select("copyLink")}>
    <div slot="icon"><IconSvg icon="copy"/></div>
    Copy Link to Clipboard
  </MenuItemWithIcon>

  <MenuDivider/>
  {/if}


  {#if options.due_date}
  
  <MenuItemWithIcon on:click={select("openGoogleCalendarDay")}>
    <div slot="icon"><IconSvg icon="calendar-go"/></div>
    Open Google Calendar
  </MenuItemWithIcon>

  <MenuItemWithIcon on:click={select("createGoogleCalendarEvent")}>
    <div slot="icon"><IconSvg icon="calendar-new"/></div>
    Create Google Calendar Event
  </MenuItemWithIcon>

  <MenuDivider/>
  {/if}
  {#if options.context != "radar"}  
    
    <MenuItemWithIcon on:click={select("nest")}>
      <div slot="icon"><IconSvg icon="nest"/></div>
      Nest New Item
    </MenuItemWithIcon>

    
    <MenuItemWithIcon on:click={select("add")}>
      <div slot="icon"><IconSvg icon="below"/></div>
      Insert New Item Below
    </MenuItemWithIcon>

    <MenuDivider/>
  {/if}  
  
  <MenuItemWithIcon on:click={select("toggleOngoing")}>
    <div slot="icon"><IconSvg icon="ongoing"/></div>
    { options.is_ongoing ? "Unset" : "Set"} as Ongoing
  </MenuItemWithIcon>  

  <MenuDivider/>

  <MenuItemWithIcon on:click={select("copy")}>
    <div slot="icon"><IconSvg icon="copy"/></div>
    Copy Text to Clipboard
  </MenuItemWithIcon>


  <MenuItemWithIcon on:click={select("hide")}>
    <div slot="icon"><IconToggleSvg value={options.is_hidden} whenTrue="hide" whenFalse="show"/></div>
    {#if options.is_hidden}
    Unhide
    {:else}  
    Hide
    {/if} Text
  </MenuItemWithIcon>  
  {#if options.context != "radar"}
  <MenuDivider/>

  <MenuItemWithIcon on:click={select("clone")}>
    <div slot="icon" class="menu-icons"><IconSvg icon="clone" classes="menu-icons"/></div>
    Clone
  </MenuItemWithIcon>

  <MenuItemWithIcon on:click={select("delete")}>
    <div slot="icon"><IconSvg icon="trash"/></div>
    Delete
  </MenuItemWithIcon>

  {/if}
</MenuContainer>

