var PouchDoc, SearchSettings;

PouchDoc = require("../PouchDb/pouch_doc.js");

SearchSettings = (function() {
  // This is a local doc, not synced
  class SearchSettings extends PouchDoc {
    after_construct(model) {
      // model is the writeable store
      this.store = model;
      return this.get();
    }

    set(setting, object) {
      this[setting] = object;
      return this.save();
    }

    set_key(setting, key, value) {
      this[setting][key] = value;
      return this.save();
    }

    get() {
      var that;
      that = this;
      return db.get(this.id).then(function(originalDoc) {
        console.log("SETTINGS GOT FORM DB");
        if (that.store != null) {
          that.depouchify(originalDoc);
          return that.store.set(that.pouchify());
        }
      }).catch(function(err) {
        var settings;
        console.error("SETTINGS");
        console.log("NOT FOUND");
        console.log(err);
        settings = that.pouchify();
        return db.put(settings).then(function(response) {
          if (that.store != null) {
            that.store.set(settings);
          }
          console.error("WROTE SETTINGS TO DB");
          return console.log(response);
        });
      }).finally;
    }

    save() {
      var that;
      that = this;
      this.store.set(that.pouchify(this));
      return db.get(this.id).then(function(originalDoc) {
        console.log("SETTINGS GOT FORM DB");
        Object.merge(originalDoc, that.pouchify(that));
        return db.put(originalDoc).then(function(response) {
          console.log("--------------updated settings doc");
          if (typeof thenCb !== "undefined" && thenCb !== null) {
            return thenCb(response);
          }
        }).catch(function(err) {
          if (typeof catchCb !== "undefined" && catchCb !== null) {
            return catchCb(err);
          }
        });
      }).catch(function(err) {
        var settings;
        console.error("SETTINGS");
        console.log("NOT FOUND");
        console.log(err);
        settings = that.pouchify();
        return db.put(settings).then(function(response) {
          if (that.store != null) {
            that.store.set(settings);
          }
          console.error("WROTE SETTINGS TO DB");
          return console.log(response);
        });
      });
    }

  };

  SearchSettings.prototype.id = "_local/search";

  SearchSettings.prototype.attribute_whitelist = ["id", "radar", "current", "favorites", "archived", "sync"];

  SearchSettings.prototype.store = void 0;

  SearchSettings.prototype.radar = {
    sort_by: "index",
    sort_descending: false,
    horizon: "7d",
    include_archived: true,
    include_completed: false,
    ongoing_only: false
  };

  SearchSettings.prototype.search = {
    sort_by: "index",
    sort_descending: false,
    horizon: "7d",
    include_archived: true,
    include_completed: true
  };

  SearchSettings.prototype.current = {
    sort_by: "index",
    sort_descending: false
  };

  SearchSettings.prototype.favorites = {
    sort_by: "index",
    sort_descending: false
  };

  SearchSettings.prototype.archived = {
    sort_by: "index",
    sort_descending: false
  };

  SearchSettings.prototype.sync = {
    enabled: false,
    remote_database: null,
    last_sync_attempt: null,
    last_successful_sync: null,
    recent_sync_messages: [],
    auto_sync: true,
    auto_sync_interval: 60000
  };

  return SearchSettings;

}).call(this);

module.exports = SearchSettings;
