<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    FlexRow
    FlexRowCell
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    IconSvg,
    IconToggleSvg,
    Indent
  } from "../../../Braintapper"
  
  

  export item = undefined
  export index = undefined
  export indent = 0
  export listId = undefined

  ###
    events
      nest
      update
      dragging
      dragStop
  ###


  draggableEl = undefined
  draggable = false

  isOver = false
  isInvalid = false

  isDragging = false

  start = (e)->
    item["listId"] = listId
    item["index"] = index || null

    e.dataTransfer.setData("text/plain", JSON.stringify(item.safe_model()))
    e.dataTransfer.setData("listid:#{listId}", JSON.stringify(item.safe_model()))
    isDragging = true
    dispatch "dragging", true

  end = (e)-> # stop dragging
    #console.error "END"
    #console.log e.type
    isDragging = false
    #dragStop = ()->
    #   console.log
    dispatch "dragStop", true
    
    # debouncedFinish = dragStop.debounce(250)
    # debouncedFinish()
    
  over = (e)->
    e.preventDefault()
    isOver = true

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false
    e.preventDefault()
    dispatch "dragStop", true
    console.log "preparse"
    console.log e.dataTransfer.getData("text/plain")
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))
    console.log "postparse"
    dropped = true

    if dropped
      dispatch "nest",
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: item
        dropIndex: index
        dropListId: listId



  mouseup = ()->
    # console.error "MOUSEUP"
    draggable = false
    isDragging = false
    isInvalid = false
    

  mousedown = ()->
    isDragging = true
    draggable = true

  getWidth = (el)->
    if el?
      el.clientWidth - 48
    else
      0


  

</script>


<div
  item-draggable
  data-id={item.id}
  class:over={isOver}
  class:invalid={isInvalid}
  {draggable}
  on:drop={drop}
  on:dragover={over}
  on:dragstart={start}
  on:dragend={end}
  on:dragleave={leave}
  bind:this={draggableEl}
>
  <FlexRow>
  
  
    <FlexRowCell flex="initial" >
      <div on:mouseup={mouseup(item)} on:mousedown={mousedown(item)} item-draggable-handle is-dragging={isDragging} visible-on-hover>
        <IconSvg classes="handle" icon="handle"/>
      </div>      
    </FlexRowCell>

    <FlexRowCell flex="true">
      <slot/>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <div item-draggable-menu  visible-on-hover>
        <slot name="menu"/>
      </div>
    </FlexRowCell>
  </FlexRow>



</div>


<style global lang="sass">

  // 24+8px = 32 for icon cols
  \:root
    --item-draggable-icon-width: 32px 
    
  

  [item-draggable]

    
    border-radius: 3px

    width: calc(100% + calc(2 * var(--item-draggable-icon-width)))
    max-width: calc(100% + calc(2 * var(--item-draggable-icon-width)))
    margin-left: calc(var(--item-draggable-icon-width) * -1)

    [item-draggable-handle], [item-draggable-menu] 
      width: var(--item-draggable-icon-width)
      svg
        path
          fill: var(--light-gray)


    [item-draggable-handle]
      width: 24px
      height: 24px
      &:hover
        cursor: grab
      &[is-dragging="true"]
        cursor: grabbing !important

    [item-draggable-menu] 
      padding-left: 8px


    [visible-on-hover]
      svg
        path
          fill: transparent
    &:hover
      background: var(--list-row-highlight)
      [visible-on-hover]
        svg
          path
            fill: var(--light-gray)
    
    &.over
      background: var(--light-blue) !important

</style>
