<script lang="coffeescript">

  import tippy from 'tippy.js'

  export content = undefined
  export position = "bottom"

  element = undefined

  tooltip = (element)->
    tippy(element, {
      content: content
      placement: position
      allowHTML: true }
    )
    return {
      destroy: () ->
        console.log "destroyed"
    }

</script>



<span data-tippy-content="Tooltip">
  <div use:tooltip><slot/></div>
</span>
