/*

  ____                      _     
 / ___|  ___  __ _ _ __ ___| |__  
 \___ \ / _ \/ _` | '__/ __| '_ \ 
  ___) |  __/ (_| | | | (__| | | |
 |____/ \___|\__,_|_|  \___|_| |_|

 */
var List, ListItem, SearchList;

// list
List = require("./List.js");

ListItem = require("./ListItem.js");

SearchList = (function() {
  class SearchList {
    constructor() {
      this["raw_items"] = [];
      this["lists"] = {};
    }

    /*

           _           _   
    ___  ___| | ___  ___| |_ 
     / __|/ _ \ |/ _ \/ __| __|
     \__ \  __/ |  __/ (__| |_ 
     |___/\___|_|\___|\___|\__|

     */
    select(keywords, options, thenCb, catchCb, finallyCb) {
      var date_selector, initialSelector, keywordSelector, selectors, that;
      // reset
      console.error("SEARCH OPTIONS");
      console.log(JSON.stringify(options, null, 2));
      this.raw_items = [];
      this.lists = {};
      that = this;
      // these will be inserted into an $and
      selectors = [
        {
          $or: [
            {
              doctype: "List"
            },
            {
              doctype: "ListItem"
            }
          ]
        }
      ];
      keywordSelector = [];
      keywords.split(" ").forEach(function(keyword) {
        return keywordSelector.append({
          name: {
            $regex: new RegExp(keyword, "gi")
          }
        });
      });
      selectors.append({
        $or: keywordSelector
      });
      switch (options.horizon) {
        case "0d":
          date_selector = {
            due_date: {
              $eq: Date.create("today").format("{yyyy}-{MM}-{dd}")
            }
          };
          break;
        case "1d":
        case "7d":
        case "14d":
          date_selector = {
            due_date: {
              $gte: Date.create(`${parseInt(options.horizon)} days ago`).format("{yyyy}-{MM}-{dd}"),
              $lte: Date.create(`${parseInt(options.horizon)} days from now`).format("{yyyy}-{MM}-{dd}")
            }
          };
          break;
        case "1m":
        case "3m":
        case "6m":
        case "12m":
          date_selector = {
            due_date: {
              $gte: Date.create(`${parseInt(options.horizon)} months ago`, {
                setUTC: true
              }).format("{yyyy}-{MM}-{dd}"),
              $lte: Date.create(`${parseInt(options.horizon)} months from now`, {
                setUTC: true
              }).format("{yyyy}-{MM}-{dd}")
            }
          };
          break;
        case "1y":
          date_selector = {
            due_date: {
              $gte: Date.create(`${parseInt(options.horizon)} years ago`, {
                setUTC: true
              }).format("{yyyy}-{MM}-{dd}"),
              $lte: Date.create(`${parseInt(options.horizon)} years from now`, {
                setUTC: true
              }).format("{yyyy}-{MM}-{dd}")
            }
          };
          break;
        default:
          date_selector = void 0;
      }
      if (date_selector != null) {
        console.error("add date selector");
        selectors.append(date_selector);
      }
      /*
      if options.ongoing_only
      selectors.append
        is_ongoing:
      $eq: true

      if !options.include_completed

        selectors.append  {
      "progress.percent_complete":
        $lt: 100 
        }

      else # due 
      console.error "DUE"
      console.log "options.include_completed #{JSON.stringify(options.include_completed)}"

      if !options.include_completed

        selectors.append  {
      "progress.percent_complete":
        $lt: 100 
        }

      */

      // first pass get items that meet criteria
      // second pass, get other items
      // 1) Missing projects - from list items
      // 2) Missing parents, from list items        
      initialSelector = {
        $and: selectors
      };
      console.error("initialSelector");
      console.log(JSON.stringify(initialSelector, null, 2));
      return db.find({
        selector: initialSelector
      }).then(function(result) {
        var firstPassIds, listItems, outputList, secondPassIds;
        
        // that.items = that.transformCollection(result.docs)
        listItems = [];
        firstPassIds = [];
        secondPassIds = [];
        outputList = [];
        firstPassIds = result.docs.map(function(doc) {
          return doc.id;
        });
        firstPassIds = firstPassIds.unique(); // remove dupes
        console.log("first pass docs");
        console.log(result.docs);
        console.log(`found: ${firstPassIds.length}`);
        // todo: pass the first pass count as # of results?
        // or use regex in the array
        that.found = firstPassIds.length;
        console.log(`THAT>FOUND: ${that.found}`);
        result.docs.forEach(function(doc) {
          switch (doc.doctype) {
            case "List":
              return that.lists[doc.id] = new List(doc);
            case "ListItem":
              that.raw_items.append(new ListItem(doc));
              secondPassIds.append(doc.list_id);
              return secondPassIds.append(doc.ancestry);
          }
        });
        secondPassIds = secondPassIds.unique().exclude(function(id) {
          return firstPassIds.includes(id);
        });
        secondPassIds = secondPassIds.map(function(o) {
          return {
            id: o
          };
        });
        console.error("secondPassIds");
        console.table(secondPassIds);
        // second pass
        return db.find({
          selector: {
            $or: secondPassIds // remove dupes
          }
        }).then(function(secondaryResults) {
          var grouped;
          secondaryResults.docs.forEach(function(doc) {
            switch (doc.doctype) {
              case "List":
                return that.lists[doc.id] = new List(doc);
              case "ListItem":
                return that.raw_items.append(new ListItem(doc));
            }
          });
          console.log(`Total Raw Items ${that.raw_items.length}`);
          grouped = that.raw_items.groupBy(function(o) {
            return o.list_id;
          });
          //console.error "grouped"

          //console.debug grouped
          Object.keys(grouped).forEach(function(group) {
            if (that.lists[group] != null) {
              return that.lists[group].setRaw(grouped[group]);
            } else {
              console.error(`orphaned project ${group}`);
              return console.debug(grouped[group]);
            }
          });
          that.raw_items.forEach(function(rawItem, index) {
            return rawItem.list_reference = that.lists[rawItem.list_id];
          });
          // todo handle orphans
          /*

           * console.log "list #{rawItem.list_id}"
           * console.debug that.lists[rawItem.list_id]
          if that.lists[rawItem.list_id]?
            console.log "[#{index}] add raw item #{rawItem.name} to #{that.lists[rawItem.list_id].name}"
            that.lists[rawItem.list_id].list_items_raw.push rawItem
            console.debug { id: rawItem.list_id, name: that.lists[rawItem.list_id].name, list_items: that.lists[rawItem.list_id].list_items_raw }
            console.log "[#{index}]  count: #{that.lists[rawItem.list_id].list_items_raw.length}"
          else # orphan
            #console.error "orphaned rawItem"
            #console.debug rawItem
            orphanCb = (result)->
              console.log "Deleted orphan"
              console.debug rawItem
            item.delete orphanCb
           */
          //console.log "LISTS"
          //console.log that.lists
          outputList = Object.keys(that.lists).map(function(key) {
            return that.lists[key];
          });
          if (thenCb != null) {
            return thenCb(outputList);
          }
        });
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  SearchList.prototype.name = "SearchList";

  SearchList.prototype.found = 0;

  return SearchList;

}).call(this);

module.exports = SearchList;
