<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    debug,
    IconSvg,
    IconToggleSvg,
    MenuContainer,
    MenuDivider,
    MenuItemWithIcon
  } from "../../../Braintapper"

  export options = 
    include_completed: true
    favorites_only: false
    sort_by: 'name'
    sort_descending: false
    context: "current"
  

  select = (value)->
    dispatch "tippy-event", { event: "select", value: value }
</script>

<MenuContainer>

  {#if options.context != "archived"}
    <MenuItemWithIcon on:click={select("toggle_completed")}>
      <span slot="icon"><IconToggleSvg value={options.include_completed} whenTrue="check"/></span>
      Include Completed Items
    </MenuItemWithIcon>
    <MenuDivider/>
  {/if}

  <MenuItemWithIcon on:click={select("toggle_sort_by_name")}>
    <span slot="icon">
          {#if options.sort_by == 'name'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Name
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("toggle_sort_by_date")}>
    <span slot="icon">
          {#if options.sort_by == 'due_date'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Date
  </MenuItemWithIcon>


</MenuContainer>
