<script lang="coffeescript">

  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    Button
  } from "../../Braintapper"

  export files = undefined
  export label = "Add File"


  getFileFromFiles = ()->
    if files?
      fileArray = Array.from(files)
      console.debug fileArray.first()    
      return fileArray.first().name
    else
      return "No file selected"

  click = (e)->
    console.log "CLICK"
    e.stopPropagation

  `$: selectedFile = getFileFromFiles(files)`
</script>


<div file-input>
<FlexRow>
  <FlexRowCell flex="initial">
    <label>
      { label }
      <input type="file" id="input" bind:files />
    </label>  
  </FlexRowCell>
  <FlexRowCell flex>
    <div selected-files>
    { selectedFile }
    </div>  
  </FlexRowCell>
</FlexRow>
</div>

<style lang="sass">
  [file-input]
    margin: 4px 0px
    height: 32px
    
  input[type="file"]
    display: none

  label
    font-family: var(--font-family-primary), -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"
    
    width: auto
    padding: 6px 12px
    border-radius: 4px

    background-color: var(--primary)
    color: var(--white)
    &:hover
      background-color: var(--primary-hover)
    &:active, &:focus
      background-color: var(--primary-active)
    &[disabled], &[disabled]:hover, &[disabled]:focus, &[disabled]:active
      background-color: var(--primary-disabled)
      color: var(--dark-gray)

  [selected-files]
    padding-left: 24px
</style>