<script lang="coffeescript">
  # /lists
  # List Index

  import { onMount } from "svelte"
  import {push, pop, replace, location} from "svelte-spa-router"


  # Data Items
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconToggleSvg,
    InlineInput 
  } from "../../Braintapper"


  import { 
    PouchLastSyncResponse
    ListHoverableContainer,
    Lists,
    List,
    ProgressPopover,
    DueDatePopover,
    QuickEntry,
    ListMenu
  } from "../../Application"


  #

  # Local Vars

  export list = []
  

  export options = {}

  export context = "archived"

  

  # List Model

  resourceList = new Lists

  refreshList = (items)->
    list = items






  # jumps

  itemUrl = "#/lists/" # links and redirects


  goToItem = (item)->
    push "#/lists/#{item.id}"

  toggleFavorite = (rootItemList)->
    #console.log "toggle Favorite"
    #console.log rootItemList

    rootItemList.is_favorite = !rootItemList.is_favorite

    list = list

    updateList
      detail:
        id: rootItemList.id
        is_favorite: rootItemList.is_favorite

  # CRUD


  #//       _                  _     _     _   
  #//   ___| | ___  _ __   ___| |   (_)___| |_ 
  #//  / __| |/ _ \| '_ \ / _ \ |   | / __| __|
  #// | (__| | (_) | | | |  __/ |___| \__ \ |_ 
  #//  \___|_|\___/|_| |_|\___|_____|_|___/\__|
  #//                                          

  cloneList = (e)->

    console.error "CloneList"
    console.log e.detail

    found = resourceList.items.find { id: e.detail.id }

    scb = ()->


      selectAndRefresh()

    if found?

      found.clone(scb)


    #console.log resourceList.items

    #resource = new RootItem

    #scb = (doc) ->
      
      
    #  clone = resource.clone()
      
    
    #resource.get(e.detail.id, scb, null, null)



  


  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted.id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null


  selectAndRefresh = (view)->
    selector =

      doctype: "List"
      $or: [
        {
          is_archived: true
        }
        
      ]    

    resourceList.select selector, refreshList

      

  
  #//                  _       _       _     _     _   
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_ 
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ 
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|
  #//       |_|                                        


  # todo: update local list items first to reduce visible lag

  updateList = (e)->

    console.log "updateRooteItem"
    console.log e.detail

    ecb = (err)->
      # refreshList()

    changedList = new List
    changedList.patch e.detail, selectAndRefresh, ecb, null



  updateListName = (e)->
    console.log e.detail


  #//      _           _                   _     _     _   
  #//   __| | ___  ___| |_ _ __ ___  _   _| |   (_)___| |_ 
  #//  / _` |/ _ \/ __| __| '__/ _ \| | | | |   | / __| __|
  #// | (_| |  __/\__ \ |_| | | (_) | |_| | |___| \__ \ |_ 
  #//  \__,_|\___||___/\__|_|  \___/ \__, |_____|_|___/\__|
  #//                                |___/                 


  destroyList = (e)->
    scb = (response)->
      resourceList.items.remove (o)->
        o.id == response  
      console.log "Deleted #{response}"
      selectAndRefresh()
    
    found = resourceList.items.find { id: e.detail.id }

    if found?
      console.error "destroy #{e.detail.id}"
      console.debug found
      found.delete scb
    




  # UI

  processList = (list, options)->

    #console.error "processLIst"
    #console.debug list

    sortableList = list.map (o)->
      # because due_date can be null, and because 
      # sortBy has issues with nulls
      # need to create a sort field
      
      if options.sort_by == "due_date"
        # always keep nulls at the bottom
        if options.sort_descending
          surrogateDate = "0000-00-00"
        else
          surrogateDate = "9999-99-99"
        o["sort_field"] = o[options.sort_by] || surrogateDate
      else
        o["sort_field"] = o.name
      return o

    filteredList = sortableList.filter (listItem)->


      result = true
      if options.favorites_only
        result = result && listItem.is_favorite


      if !options.include_completed
        result = result && (listItem.progress.percent_complete < 100)

      return result

    if list?
      return filteredList.sortBy "sort_field", options.sort_descending
        

    else
      []


  onMount ()->
    PouchLastSyncResponse.subscribe (response)->
      unless response.handled
        console.error "PouchLastSyncResponse changed"
        console.log response      
        if response.direction == "pull"
          console.log "a pull occurred"
          pulledDocs = response.change.docs

          if pulledDocs.length > 0
            console.log "some docs were pulled, check to see if route was affected"
            affectedDocsForUrl = pulledDocs.filter (o)->
              return (o.doctype == "List" ) 

            console.log "Affected docs = #{affectedDocsForUrl}"
            console.log affectedDocsForUrl      
            console.error "should refresh!"
            if affectedDocsForUrl.length > 0
              selectAndRefresh()
          else
            # No changed documents
            console.info "No sync changes related to this view"
          response.handled = true
          PouchLastSyncResponse.set response

        # todo make this smarter by handling edits, adds and deletions differently



  `$: sortedlist = processList(list, options)`
  `$: selectAndRefresh(context)`

</script>



{#if list && list.length}
  {#each sortedlist as item, index}
    <ListHoverableContainer style="margin-bottom: 12px; padding: 4px 4px" id={item.id}>
      <div slot="pre">
        <div is-favorite={item.is_favorite}>
          <ButtonIcon on:click={toggleFavorite(item)}>
            <IconToggleSvg value={item.is_favorite} whenTrue="star" whenFalse="star-empty" />
          </ButtonIcon>       
        </div>     
      </div>
      <div>
        <FlexRow>
          <FlexRowCell flex="initial" > <!-- class:favorited={item.is_favorite} -->
          
          </FlexRowCell>            
          <FlexRowCell flex="initial">
            <ProgressPopover {item} value={item.progress} on:update={updateList} on:open on:close idAttribute="id"/>
          </FlexRowCell>

          <FlexRowCell flex>
            <InlineInput item={item} on:update={updateList} on:clicked={goToItem(item)}>
              <FormatEllipsis>
                <a href="{itemUrl}{item.id}" on:click|preventDefault><em>{item.name || "No Names"}</em></a>
              </FormatEllipsis>
              
            </InlineInput>
          </FlexRowCell>

          <FlexRowCell flex="initial">
            <div layout="row">
              <div flex="initial" present={item.due_date != null}>
                <DueDatePopover {item} value={item.due_date} on:update={updateList} idAttribute="id" on:open on:close/>
              </div>

            </div>
          </FlexRowCell>
          <FlexRowCell flex="initial">
            

          </FlexRowCell>
        </FlexRow>
      </div>
      <div slot="menu">
        <ListMenu {item} on:select={updateList} on:clone={cloneList} on:destroy={destroyList}/>
      </div>
    </ListHoverableContainer>
  {/each}
{/if}

<style lang="sass">

  [is-favorite="true"]
    svg
      path
        fill: blue  !important


  // legacy
  // global to ensure child menus inherit styles

  [type="list"]

    // border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 64px)
    max-width: calc(100% + 64px)
    margin-left: -32px // 24+8px for icon cols

    margin-bottom: 8px

    [item-interior]
      width: 100%
      max-width: 100%

    [favorite], [menu], [progress] // set cell wdiths
      width: 32px

    [favorite]
      svg
        path
          fill: transparent


    [favorite].favorited
      svg
        path
          fill: var(--lightest-gray)

    [menu]
      text-align: center


    &:hover
      [favorite]
        svg
          path
            fill: var(--blue)

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent

    &:hover
      background: #fcfcfc
      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)



</style>
