<script lang="coffeescript">
  # /lists
  # List Index


  import {push } from "svelte-spa-router"


  # Data Items
  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { debug, IconSvg } from "../../Braintapper"
  debug.on = window.debug
  
  import { Lists, PageContent, PageHeader, ListsMenu, QuickEntry } from "../../Application"

  


  


  # Common Layout Components


  import List from './_List.svelte'

  # List Item Components


  




  # List Model

 

  resourceList = new Lists

  getOrSet = (parameter, override)->
    if localStorage.getItem(parameter)?
      return localStorage.getItem(parameter)  
    else
      localStorage.setItem parameter, override
      return override


  viewOptions =
    include_completed: getOrSet("radar_lists_include_completed", true) == "true"
    favorites_only: getOrSet("radar_lists_favorites_only", false) == "true"
    sort_by: getOrSet("radar_lists_sort_by", "name")
    sort_descending: getOrSet("radar_lists_sort_descending", false) == "true"
    context: "archived"
  

  # lists_sort_descending
  # lists_sort_by
  # lists_
  # ongoingOnly = getOrSet("radar_ongoing_only", false) == "true"
  



  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted.id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null


  menuSelect = (e)->
    console.error "menuSelect #{e.detail}"
    switch e.detail
      when "current", "archived"
        viewOptions.context = e.detail
        localStorage.setItem "radar_lists_view", e.detail
      when "toggle_favorites"
        viewOptions.favorites_only = !viewOptions.favorites_only 
        localStorage.setItem "radar_lists_favorites_only", viewOptions.favorites_only
      when "toggle_completed"
        viewOptions.include_completed = !viewOptions.include_completed 
        localStorage.setItem "radar_lists_include_completed", viewOptions.include_completed

      when "toggle_sort_by_name"
        localStorage.setItem "radar_lists_sort_by", "name"
        if viewOptions.sort_by != "name" 
          viewOptions.sort_by = "name"
          viewOptions.sort_descending = false
        else
          viewOptions.sort_descending = !viewOptions.sort_descending
          
        localStorage.setItem "radar_lists_sort_descending", viewOptions.sort_descending
        
      when "toggle_sort_by_date"
        localStorage.setItem "radar_lists_sort_by", "due_date"
        if viewOptions.sort_by != "due_date" 
          viewOptions.sort_by = "due_date"
          viewOptions.sort_descending = false
        else
          viewOptions.sort_descending = !viewOptions.sort_descending
          
        localStorage.setItem "radar_lists_sort_descending", viewOptions.sort_descending
        
      else
        console.log "do nothing #{e.detail}"

  pageName = "Inactive Lists"


  `$: context = viewOptions.context`
</script>

<svelte:head>
  <title>{pageName}</title>
</svelte:head>



<PageContent>
    <PageHeader icon={true}>
      <div slot="icon">
          <IconSvg icon="archive" size="36"/>
      </div>
      <FlexRow style="max-width: 100%;">
        <FlexRowCell flex style="overflow-x: hidden">
          <h1 title>{pageName}</h1>
        </FlexRowCell>
        <FlexRowCell style="padding-top: 16px; width: 32px" flex="initial">
          <ListsMenu on:select={menuSelect} options={viewOptions}/>
        </FlexRowCell>
      </FlexRow>

    </PageHeader>

  <section>

    <List context={context} options={viewOptions}/>
    

    <QuickEntry on:add={addRootItem} stubPlaceholder="Add List" />
    
  </section>
</PageContent>

<style lang="sass">
  svg
    path
      fill: var(--light-gray)
</style>