<script lang="coffeescript">

  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug,
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent,
    InlineInput 
    SearchHighlighter
  } from "../../Braintapper"


  import { 
    ProgressPopover,  
    DueDatePopover, 
    ListItemMenu,
    ListHoverableContainer
    
    } from "../../Application"


  




  export keywords = ""
  export list = undefined
  export listId = undefined



  goToList = ()->
    location.href="#/lists/#{listId}"


  toggleExpanded = (item)->
    console.log "toggle Expanded"
    
    item.is_expanded.radar = !item.is_expanded.radar
    dispatch "update", {id: item.id, is_expanded: {radar: item.is_expanded.radar, list: item.is_expanded.list} }

  

  updateListItem = (e)->
    console.log "Radar._ListItems menu"
    console.log e.detail
    if listId?
      console.log "top event listid: #{listId}"
      e.detail["list_id"] = listId
    dispatch "update", e.detail



  destroyDispatcher = (e)->
    dispatch "destroy", e.detail
    showDeleteConfirmation = false


</script>


{#each list.list_items as listItem, index}
  
  <ListHoverableContainer>
    <div type="radar-item">
      <FlexRow classes="radar-item">

        
        <FlexRowCell flex="initial" style="width: {(listItem.ancestry || []).length * 32}px">
          <Indent level={(listItem.ancestry || []).length} width={32}/>
        </FlexRowCell>
            
        <FlexRowCell classes="collapsible">
          <div collapsible>
            {#if (listItem.list_items || []).length > 0}
              <ButtonIcon on:click={toggleExpanded(listItem)}>
                <IconToggleSvg value={listItem.is_expanded.radar} whenTrue="expanded" whenFalse="collapsed"/>
              </ButtonIcon>
              
            {:else}
              <IconSvg/>
            {/if}
          </div>
        </FlexRowCell>    
        <FlexRowCell flex>
          <FlexRow>
            <FlexRowCell flex="initial">
              <div progress>
                <ProgressPopover item={listItem} value={listItem.progress} on:update={updateListItem} on:open on:close/>
              </div>
            </FlexRowCell>
            <FlexRowCell flex style="width: 100px">
              
                {#if listItem.is_hidden}<FormatEllipsis>Text for this is hidden. You can edit it to see it, or unhide it using the menu.</FormatEllipsis>
                {:else}<FormatEllipsis><SearchHighlighter text={listItem.name || "No Name"} keywords={keywords.split(" ")}/></FormatEllipsis>&nbsp;
                {/if}            
                {#if listItem.is_ongoing}
                  &nbsp;
                  <span><IconSvg icon="ongoing" size="12"/></span>
                {/if}
              
              
            </FlexRowCell>
            <FlexRowCell flex="initial">
              <div visible-on-hover>
                <DueDatePopover item={listItem} value={listItem.due_date} on:update={updateListItem} on:open on:close/>
              </div>
            </FlexRowCell>
          
          </FlexRow>
        </FlexRowCell>

      </FlexRow>
    </div>
    <div slot="menu">
      <ListItemMenu item={listItem} on:update={updateListItem} on:destroy={destroyDispatcher} context="radar"/>
    </div>
  </ListHoverableContainer>

    <!-- children -->
    {#if (listItem.list_items || []).length > 0}
      {#if listItem.is_expanded.radar }
        <svelte:self list={listItem} {listId} on:update={updateListItem} on:destroy={destroyDispatcher} {keywords}/>
      {/if}
    {/if}
  
{/each}


<style lang="sass">

  [type="radar-item"]

    background: transparent

    [item-interior]
      width: calc(100%)
      max-width: calc(100%)

    [collapsible], [menu], [progress] // set cell wdiths
      width: 32px


    [menu]
      text-align: center

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent
      &[is-showing="true"]
        svg
          path
            fill: var(--blue) !important
      
    



    [collapsible]
      line-height: 24px
      svg, svg.menu
        display: inline


    &:hover

      

      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)      
      [handle], [collapsible], [draggable-item-menu]
        svg, svg.menu
          display: inline


      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)



      [collapsible]
        button[icon]
          padding: 0px !important
        svg
          path.fill
            fill: var(--light-gray)
        &:hover
          svg
            path
              fill: var(--blue)


</style>
