<script lang="coffeescript">
  import marked from "marked"

  content = undefined

  window._content = content

  checkContent = (item)->
    if item?
      return item.innerHTML
    else
      return ""

  `$: markup = checkContent(content)`
</script>
<div>
  <div markup bind:this={content}>
    <slot/>
  </div>
  <div>{@html marked(markup)}</div>
</div>

<style lang="sass">
  [markup]
    display: none
</style>