<script lang="coffeescript">


  export value = null
  export events = []
  export isShowing = undefined
  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { ButtonIcon, IconSvg, IconToggleSvg } from "../../Braintapper"
  import { createEventDispatcher, onDestroy  } from 'svelte'
  dispatch = createEventDispatcher()

  weeks = []

  # console.log events

  eventsForDay = (day)->

    if events.length ==  0
      return []
    else
      return events.filter (event)->
        return Date.create(event.start).format("{MM}{dd}") == Date.create(day).format("{MM}{dd}")





  currentCalendar = []
  currentMonth = null
  currentCalendarHeader = null

  generate = (baseDate)->
    #console.error "generate"
    #console.log JSON.stringify(baseDate)
    outputWeeks = []
    currentCalendarHeader = baseDate.format("{Month} {yyyy}")
    currentMonth = baseDate.format("{M}")
    calendarStart = baseDate.clone().beginningOfMonth().beginningOfWeek()
    calendarEnd = baseDate.clone().endOfMonth().endOfWeek()
    runningDate = calendarStart.clone()
    #console.error "runningDate: #{runningDate}"
    currentWeek = []
    count = 0
    while runningDate.isBetween(calendarStart, calendarEnd)
      currentWeek.push runningDate.clone()
      runningDate.addDays(1)
      count++
      if count == 7
        outputWeeks.push currentWeek
        currentWeek = []
        count = 0
    weeks = outputWeeks

  displayDate = undefined


  current = (dateValue)->
    # console.error "CURRENT"

    # console.log "dateValue"
    # console.log dateValue
    if value?
      # console.log "value not null"
      # console.log "Value"
      # console.log value
      if Object.isDate(dateValue)
        # console.log "IS ALREADY A DATE"
        displayDate = dateValue
      else
        # console.log "NOT A DATE, MAKIE IT A DATE"
        displayDate = Date.create(dateValue)
    else
      # console.log "NO VALUE, today"
      displayDate = Date.create()
    # console.log "displayDate"
    # console.log displayDate
    # console.error "-------------------------------------"

    

  next = ()->
    #console.error "next()"
    #console.log displayDate
    #console.log Object.isDate(displayDate)
    displayDate.addMonths(1)
    #console.log "displayDate #{value}"
    #console.log "value #{value}"    
    #console.log value
    generate(displayDate)



  previous = ()->
    displayDate.addMonths(-1)
    generate(displayDate)
    # console.log "value #{value}"

  nextYear = ()->
    displayDate.addYears(1)
    generate(displayDate)
    # console.log "value #{value}"


  previousYear = ()->
    displayDate.addYears(-1)
    generate(displayDate)
    # console.log "value #{value}"

  today = ()->
    setDay Date.create()
    # console.log "today"

  yesterday = ()->
    setDay Date.create("yesterday")

  tomorrow = ()->
    setDay Date.create("tomorrow")



  pushMode = 1 # forward

  pushDay = (interval)->
    targetInterval = "#{(interval || 1) * pushMode} day"
    #console.log "target interval #{targetInterval}"
    target = displayDate.advance(targetInterval)
    #console.error "push day #{target}"
    setDay target



  pushWeek = ()->
    target = displayDate.advance("#{(7) * pushMode} days")
    #console.error "push week #{target}"
    setDay target


  pushMonth = ()->
    target = displayDate.advance("#{(1) * pushMode} month")
    #console.error "push month #{target}"
    setDay target


  pushQuarter = ()->
    target = displayDate.advance("#{(3) * pushMode} months")
    #console.error "push quarter #{target}"
    setDay target


  pushYear = ()->
    target = displayDate.advance("#{(1) * pushMode} year")
    #console.error "push year #{target}"
    setDay target

  


  setDay = (day)->
    # console.log "CALENDAR SETDAY #{day}"
    pushMode = 1 # reset
    dispatch "change", day

  

  togglepushMode = ()->
    pushMode = pushMode * -1


  setNextWeekday = (weekday)-> # jump to the next specified weekday

    # sun = 0
    # sat = 6

    # if current weekday = weekday, then it''s next week
    #console.error "setNextWeekday"
    #console.log "displayDate getWeekday: #{displayDate.getWeekday()}"
    #console.log "weekday = weekday #{weekday}"


    #console.log "weekday >  displayDate.getWeekday  : #{weekday >  displayDate.getWeekday()}"
    if weekday >  displayDate.getWeekday()
      # same week
      differential = weekday - displayDate.getWeekday()
    else 
      # next week
      differential = 7 - displayDate.getWeekday() + weekday
    target = displayDate.advance("#{differential} days")
    setDay target

  onDestroy (e)->
    # console.error "DESTROY"

  setIsShowing = (state)->
    #console.error "==== setIsShowing"
    #console.log "isShowing: #{state}"
    #console.log "value: #{value}"
    #console.log "displayDate: #{displayDate}"
    if !state # reset
      pushMode = 1
      current(value)
      

  `$: current(value)`
  `$: generate(displayDate)`
  `$: isPopoverActive = setIsShowing(isShowing)`


</script>





<div container>
  <table calendar>
    <thead>
      <tr>
        <td colspan="7">
          <FlexRow>
            <FlexRowCell style="width: 33%">
              <button type="button" on:click={yesterday} dayshortcut>
                Yesterday
              </button>
            </FlexRowCell>
            <FlexRowCell flex="flex" style="width: 33%">
              <button type="button" on:click={today} dayshortcut>
                Today
              </button>
              </FlexRowCell>
            <FlexRowCell style="width: 33%">
              <button type="button" on:click={tomorrow} dayshortcut>
                Tomorrow
              </button>

            </FlexRowCell>
          </FlexRow>
        </td>
      </tr>
      <tr>
        <td colspan="7">
          <FlexRow>
            <FlexRowCell>
              <ButtonIcon on:click={previousYear}>
                <IconSvg icon="previous-year"/>
              </ButtonIcon>

              <ButtonIcon on:click={previous}>
                <IconSvg icon="previous"/>
              </ButtonIcon>
            </FlexRowCell>
            <FlexRowCell flex="flex"><div monthname>{currentCalendarHeader}</div></FlexRowCell>
            <FlexRowCell>
              <ButtonIcon on:click={next}>
                <IconSvg icon="next"/>
              </ButtonIcon>
              <ButtonIcon on:click={nextYear}>
                <IconSvg icon="next-year"/>
              </ButtonIcon>
            </FlexRowCell>
          </FlexRow>
        </td>
      </tr>
      <tr weekdays>
        <td><button on:click={setNextWeekday(0)}>S</button></td>
        <td><button on:click={setNextWeekday(1)}>M</button></td>
        <td><button on:click={setNextWeekday(2)}>T</button></td>
        <td><button on:click={setNextWeekday(3)}>W</button></td>
        <td><button on:click={setNextWeekday(4)}>T</button></td>
        <td><button on:click={setNextWeekday(5)}>F</button></td>
        <td><button on:click={setNextWeekday(6)}>S</button></td>
      </tr>
    </thead>
    <tbody>
      {#each weeks as week, weekIndex}
        <tr>

          {#each week as day, dayindex}
            {#if (parseInt(day.format("{M}")) != parseInt(currentMonth)) }
              {#if (weekIndex < 1) }
                <td month="previous" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {:else}
                <td month="next" events={eventsForDay(day).length} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
              {/if}
            {:else}
              <td month="current" events={eventsForDay(day).length} today={day.isToday()} selected={day.is(value)}><button on:click={setDay(day)}>{day.format("{d}")}</button></td>
            {/if}
          {/each}
        </tr>
      {/each}
    </tbody>
    <tfoot>
      <tr>
        <td colspan="7">
          <FlexRow>
            <FlexRowCell style="width: 15%">
              <ButtonIcon on:click={togglepushMode}>     
                <IconToggleSvg value={pushMode == 1} whenTrue="calendar-go" whenFalse="calendar-back"/>           
              </ButtonIcon>
            </FlexRowCell>            
            <FlexRowCell style="width: 17%">
              <button pushshortcut type="button" on:click={pushDay(1)}>D</button>
            </FlexRowCell>            
            <FlexRowCell style="width: 17%">
              <button pushshortcut type="button" on:click={pushWeek}>W</button>
            </FlexRowCell>
            <FlexRowCell flex="flex" style="width: 17%">
              <button pushshortcut type="button" on:click={pushMonth}>M</button>
              </FlexRowCell>
            <FlexRowCell style="width: 17%">
              <button pushshortcut flex="flex" type="button" on:click={pushQuarter}>Q</button>
            </FlexRowCell>
            <FlexRowCell flex="flex" style="width: 17%">
              <button pushshortcut type="button" on:click={pushYear}>Y</button>
            </FlexRowCell>
          </FlexRow>
        </td>
      </tr>
      <tr>
        <td colspan="7">
          <FlexRow>
            <FlexRowCell style="width: 15%">
              <ButtonIcon on:click={togglepushMode}>     
                <IconToggleSvg value={pushMode == 1} whenTrue="calendar-add" whenFalse="calendar-remove"/>           
              </ButtonIcon>            
            </FlexRowCell>            
            <FlexRowCell style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(14)}>14</button>
            </FlexRowCell>
            <FlexRowCell flex="flex" style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(30)}>30</button>
              </FlexRowCell>
            <FlexRowCell style="width: 21%">
              <button pushshortcut flex="flex" type="button" on:click={pushDay(90)}>90</button>
            </FlexRowCell>
            <FlexRowCell flex="flex" style="width: 21%">
              <button pushshortcut type="button" on:click={pushDay(180)}>180</button>
            </FlexRowCell>
          </FlexRow>
        </td>        
      </tr>
    </tfoot>

  </table>
</div>

<style lang="sass">
  [container]
    display: block
  table
    padding: 14px
    width: 100%

  tfoot
    td
      border-top: 1px solid var(--lightest-gray)
      padding: 4px 0px
  button[dayshortcut],button[pushshortcut]
    background: transparent
    text-align: center
    text-transform: uppercase
    width: 100%
    &:hover
      color: var(--blue)

  button[pushshortcut]      
    padding: 1px 12px
    font-size: 16px
    font-weight: bold

  [monthname]
    font-weight: bold
    text-align: center
    vertical-align: middle
    padding: 0px
    font-size: 16px
    text-transform: uppercase



  tr[weekdays]
    td
      text-align: center
      padding: 3px 0px

  tbody
    td
      text-align: center
      vertical-align: middle
      height: 32px
      width: 32px
      font-weight: bold

  [month=previous], [month=next]
    button
      color: var(--lighter-gray)



  thead
    button
      padding: 0px
      background: none
      border: none
      &:hover
        color: blue
  tbody
    td[events]
      background: tan
    td[events="0"]
      background: none

    button
      background: none
      text-align: center
      vertical-align: middle
      height: 100%
      width: 100%
      border-radius: var(--corner-m)


  [today="true"]
    button
      border: 1px solid var(--lightest-blue)

  [selected="true"]
    button
      background-color: var(--blue)
      color: var(--white)


  [skip-icon]
    path
      fill: var(--light-blue)



</style>
