<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import {
    debug,
    IconSvg,
    IconToggleSvg,
    MenuContainer,
    MenuDivider,
    MenuItemWithIcon
  } from "../../../Braintapper"



  export options = 
    include_completed: true
    ongoing_only: true
    sort_by: 'index'
    sort_descending: true
  



  select = (value)->
    dispatch "tippy-event", { event: "select", value: value }


</script>

<MenuContainer>

  <MenuItemWithIcon on:click={select("toggle_ongoing")}>
    <span slot="icon"><IconToggleSvg value={options.ongoing_only} whenFalse="check"/></span>
    Items Due
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("toggle_ongoing")}>
    <span slot="icon"><IconToggleSvg value={options.ongoing_only} whenTrue="check"/></span>
    Ongoing
  </MenuItemWithIcon>


  <MenuDivider/>
  <MenuItemWithIcon on:click={select("toggle_completed")}>
    <span slot="icon"><IconToggleSvg value={options.include_completed} whenTrue="check"/></span>
    Include Completed Items
  </MenuItemWithIcon>

  <MenuDivider/>



  <MenuDivider/>


  <MenuItemWithIcon on:click={select("toggle_sort_none")}>
    <span slot="icon"><IconToggleSvg value={options.sort_by == 'index'} whenTrue="check"/></span>
    No Sorting
  </MenuItemWithIcon>

  
  <MenuDivider/>

  <MenuItemWithIcon on:click={select("toggle_sort_name")}>
    <span slot="icon">
          {#if options.sort_by == 'name'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Name
  </MenuItemWithIcon>
  <MenuItemWithIcon on:click={select("toggle_sort_date")}>
    <span slot="icon">
          {#if options.sort_by == 'due_date'}
            <IconToggleSvg value={options.sort_descending} whenTrue="arrow-down" whenFalse="arrow-up"/>
          {/if}
      </span>
    Sort By Date
  </MenuItemWithIcon>


</MenuContainer>

