<script lang="coffeescript">
  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  import Progress from "./_Progress.svelte"

  export style = undefined
  export item = undefined
  export value = { percent_complete: 0, color: "light-gray" }
  export size = 24

  tippyObject = undefined
  component = undefined
  isShowing = false


  export idAttribute = "id"

  import {IconProgressCircle, TippyPopover} from "../../../Braintapper"
  
  tippyComponent = undefined
  tippyObject = undefined

  setProgress = (e)->
    console.log "setProgress"
    console.debug e.detail
    tippyComponent.$set { progressValue: e.detail }
    eventObject = {  progress: e.detail }
    eventObject[idAttribute] = item[idAttribute]
    dispatch "update", eventObject
    tippyObject.hide()


</script>


<TippyPopover template={Progress} on:setProgress={setProgress} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} theme="progress">
  <div is-showing={isShowing} {style}>
    {#if item.progress }
    <IconProgressCircle value={item.progress.percent_complete} color={item.progress.color} {size}/>
    {/if}
  </div>
</TippyPopover>

<style global lang="sass">

  .tippy-box
    &[data-theme="progress"]
      background-color: var(--white)
      border: 1px solid var(--gray)
      border-radius: 0
      width: 180px

</style>
