<script lang="coffeescript">
  import { onMount, createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()


  import {push, pop, replace, location} from 'svelte-spa-router'
  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 

    debug, 
    ButtonIcon,
    FormatEllipsis
    IconSvg,
    IconToggleSvg,
    Indent, 
    MenuContainer
  } from "../../Braintapper"

  import { 

    PouchLastSyncResponse
    PouchRadarSettings
    Lists
    List
    ListItem
    RadarList
    PageContent
    PageHeader
    RadarMenu
    ListHoverableContainer
    ProgressPopover
    DueDatePopover
    TimeHorizon 
          
  } from "../../Application"

  debug.on = false
  # debug.context = "Radar/Index.svelte"

  mounted = false



  import RadarItems from './_ListItems.svelte'

  

  # //   _                 _ ____  _                             
  # //  | | ___   ___ __ _| / ___|| |_ ___  _ __ __ _  __ _  ___ 
  # //  | |/ _ \ / __/ _` | \___ \| __/ _ \| '__/ _` |/ _` |/ _ \ 
  # //  | | (_) | (_| (_| | |___) | || (_) | | | (_| | (_| |  __/
  # //  |_|\___/ \___\__,_|_|____/ \__\___/|_|  \__,_|\__, |\___|
  # //                                                |___/      

  

  getOrSet = (parameter, override)->
    if localStorage.getItem(parameter)?
      return localStorage.getItem(parameter)  
    else
      localStorage.setItem parameter, override
      return override


  # testRouteEvent()

  #
  # filters
  #

  includeCompleted = getOrSet("radar_include_completed", true) == "true"
  ongoingOnly = getOrSet("radar_ongoing_only", false) == "true"
  horizon = getOrSet("radar_horizon", "14d")

  sortBy = getOrSet("radar_sort_by", "index")
  sortDescending = getOrSet("radar_sort_descending", false) == "true"


  options =
    include_completed: getOrSet("radar_include_completed", true) == "true"
    ongoing_only: getOrSet("radar_ongoing_only", false) == "true"
    sort_by: getOrSet("radar_sort_by", "index")
    sort_descending: getOrSet("radar_sort_descending", false) == "true"
    horizon: getOrSet("radar_horizon", "14d")

  #
  # filter functions
  #
  
  updateHorizon = (e)->
    horizon = e.detail
    options.horizon = horizon
    localStorage.setItem "radar_horizon", horizon



  handleRadarMenu = (e)->
    debug.log "handleRadarMenu::#{e.detail}"
    switch e.detail
      when "toggle_completed"
        includeCompleted = !includeCompleted
        options.include_completed = includeCompleted
        localStorage.setItem "radar_include_completed", includeCompleted
      when "toggle_ongoing"
        ongoingOnly = !ongoingOnly
        options.ongoing_only = ongoingOnly
        localStorage.setItem "radar_ongoing_only", ongoingOnly      
        
      when "toggle_sort_none"
        sortBy = "index"
        sortDescending = false
        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending
      when "toggle_sort_name"
        if sortBy == "name" # same as before
          sortDescending = !sortDescending
        else
          sortDescending = false
        sortBy = "name"
        
        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending
      when "toggle_sort_date"
        if sortBy == "due_date" # same as before
          sortDescending = !sortDescending
        else
          sortDescending = false      
        sortBy = "due_date"

        options.sort_by = sortBy
        options.sort_descending = sortDescending
        localStorage.setItem "radar_sort_by", sortBy
        localStorage.setItem "radar_sort_descending", sortDescending


  jump = (anchorName)->
    elem = u("[name=#{anchorName}]")
    scrollContainer = u("body > main > section").first()
    scrollContainer.scrollTop = elem.first().offsetTop - 40

  list = []

  ###

    ____                                      _     _     _   
   |  _ \ ___  ___  ___  _   _ _ __ ___ ___  | |   (_)___| |_ 
   | |_) / _ \/ __|/ _ \| | | | '__/ __/ _ \ | |   | / __| __|
   |  _ <  __/\__ \ (_) | |_| | | | (_|  __/ | |___| \__ \ |_ 
   |_| \_\___||___/\___/ \__,_|_|  \___\___| |_____|_|___/\__|
                                                              

  ###

  resource = new RadarList()

  

  # updatedOptions is ignored, just there to force change. It is not a valid options object
  getRadarLists = (updatedOptions)-> 
    # debug.checkpoint "getRadarLists", "-"
    #console.log "getRadarLists"
    # console.log options
    listsLoadedScb = (lists)->
      #debug.checkpoint "getRadarLists: loadedCB"
      console.log "got readar lsits"
      console.log lists
      #debug.checkpoint "getRadarLists: treeify start"
      lists.forEach (childList)->
        # console.log childList
        childList.treeify()
        #console.log childList
        # list.append childList
        # list = list # force change
      console.log "list"
      console.debug list
      list = lists

      #debug.checkpoint "getRadarLists: treeify end"
      
      #debug.checkpoint "getRadarLists: sort lists"
      # list = lists
      #debug.checkpoint "getRadarLists: sort done, should paint"
      # console.debug lists
    if mounted
      debug.checkpoint "getRadarLists: resource.select"
      resource.select(options , listsLoadedScb, null, null)
    

  

  destroyListItem = (e)->




  export list = []

  style = ":hover { font-weight: bold }"

  goToItem = (p)->
    #console.log "gotoitem"
    #console.log "#/lists/#{p._id}"
    push "#/lists/#{p._id}"

  refresh = ()->
    dispatch "refresh"


  #//                  _       _       _     _     _   
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_ 
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ 
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|
  #//       |_|                                        

  updateList = (e)->
    # Update Project Attribute

    found = list.find { id: e.detail.id }

    debug.table "updateList found", found, true
    debug.object "e.detail", e.detail
    console.error "---------------------updateList"
    console.log e.detail
    if found?
      Object.merge found, e.detail
      found.patch e.detail, getRadarLists


  #//                  _       _       _     _     _   ___ _                 
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//       |_|                                                              

  updateListItem = (e)-> 

    #console.log "Radar.Index update list item"
    #console.log e.detail

    #console.log resource
    found = resource.raw_items.find { id: e.detail.id }
    #console.error "found"
    #console.debug found

    Object.merge found, e.detail
    if found?
      found.patch e.detail, getRadarLists
    else
      console.log "item not found"





    
  toggleExpanded = (list)->
    console.log "toggle Expanded #{list.name}"
    list.is_expanded.radar = !list.is_expanded.radar
    updateList { detail: {  id: list.id, is_expanded: list.is_expanded } }
  


  horizonRange = (range, includeCompletedFlag, ongoingOnlyFlag, sortByFlag, sortDescendingFlag)->


    itemsLabel = "in progress items"
    if includeCompletedFlag
      itemsLabel = "all items"

    ongoingSuffix = " as well as items flagged as ongoing"
    if ongoingOnlyFlag
      itemsLabel = "only items flagged as ongoing"
      ongoingSuffix = ""
    





    sortDescendingLabel = ""
    if sortDescendingFlag
      sortDescendingLabel = " (descending)"



    sortLabel = ""
    
    switch sortByFlag
      when "name"
        sortLabel = ", sorted by name#{sortDescendingLabel}"
      when "due_date"
        sortLabel = ", sorted by date#{sortDescendingLabel}"

      


    rangeLabel = ""

    switch range
      when "0d"
        rangeLabel = "for today"
      when "1d"
          rangeLabel = "for yesterday, today and tomorrow"
      when "1d","7d","14d"
        rangeLabel = " between #{Date.create().addDays(parseInt(horizon) * -1, true).medium()} and #{Date.create().addDays(parseInt(horizon) , true).medium()}"
        # Math.abs(Date.create().beginningOfDay().daysSince(Date.create().beginningOfDay())) 
      when "1m","3m","6m","12m"
        rangeLabel = " between #{Date.create().addMonths(parseInt(horizon) * -1, false).medium()} and #{Date.create().addMonths(parseInt(horizon) , false).medium()}"
      when "1y"
        rangeLabel = " between #{Date.create().addYears(parseInt(horizon) * -1, false).medium()} and #{Date.create().addYears(parseInt(horizon) , false).medium()}"
      else
        rangeLabel = "for all time"



    
    "Showing #{itemsLabel}#{ongoingSuffix} items #{rangeLabel}#{sortLabel}."


  #//              __  __                   _   
  #//   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
  #//  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
  #// | (_) | | | | |  | | (_) | |_| | | | | |_ 
  #//  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
                                             

  onMount ()->
    mounted = true

    PouchRadarSettings.subscribe  (val)->
      console.log "Radar on mount setting changed"
      console.log val

    PouchLastSyncResponse.subscribe (response)->
      unless response.handled
        console.error "PouchLastSyncResponse changed"
        console.log response      
        if response.direction == "pull"
          console.log "a pull occurred"
          pulledDocs = response.change.docs

          if pulledDocs.length > 0
            console.log "some docs were pulled, check to see if route was affected"
            getRadarLists()
          else
            # No changed documents
            console.info "No sync changes related to this view"
          response.handled = true
          PouchLastSyncResponse.set response

        # todo make this smarter by handling edits, adds and deletions differently
 



  `$: getRadarLists(options)`
  `$: sortedList = list.sortBy(sortBy, sortDescending)`


</script>


<svelte:head>
  <title>Radar</title>
</svelte:head>

<PageContent>
  <PageHeader>
      <FlexRow>
        <FlexRowCell flex>
          <h1 title>Radar</h1>
        </FlexRowCell>
        <FlexRowCell style="padding-top: 16px">
          <RadarMenu options={options} on:select={handleRadarMenu}/>
        </FlexRowCell>
      </FlexRow>  
  </PageHeader>

  <TimeHorizon  value={horizon} on:update={updateHorizon}/>
  

  <div horizon-label>
    <p>{ horizonRange(horizon, includeCompleted, ongoingOnly, sortBy, sortDescending) }</p>
    <p><i>Ongoing items without a date are always shown.</i></p>
    <p>Archived Lists are excluded from the Radar view.</p>
  </div>
  <section list>

    {#if sortedList.length > 0}
      {#each sortedList as list }

        <section radar>
          <!-- list -->
          <ListHoverableContainer>
              <div slot="pre">
                <div collapsible>
                  <ButtonIcon on:click={toggleExpanded(list)} style="height: 32px; width: 32px; left: -6px; position: relative">
                    <IconToggleSvg value={list.is_expanded.radar} whenTrue="expanded" whenFalse="collapsed" size="32"/>
                  </ButtonIcon>
                </div>              
              </div>
              <div>
                <FlexRow classes="radar-list" style="line-height: 36px">


                  <FlexRowCell flex="initial" style="padding-right: 8px; padding-top: 3px">
                    <ProgressPopover item={list} value={list.progress} on:update={updateList} on:open on:close/>
                  </FlexRowCell>

                  <FlexRowCell flex>
                    <FormatEllipsis>
                      <h3><a href="#/lists/{list.id}">{list.name || "No Name"}</a></h3>
                    </FormatEllipsis>
                      

                  </FlexRowCell>

                  <FlexRowCell flex="initial">
                    <div layout="row">
                      <div flex="initial" present={list.due_date != null} visible-on-hover>
                        <DueDatePopover item={list} value={list.due_date} on:update={updateList} on:open on:close/>
                      </div>
                    </div>
                  </FlexRowCell>

                  
              </FlexRow>
            </div>
            <div slot="menu"><IconSvg/></div>
          </ListHoverableContainer>
          <hr/>

          <!-- list items -->

          {#if list.list_items}
            {#if list.list_items.length > 0}
              {#if list.is_expanded.radar == true}
              <RadarItems list={list} on:destroy={destroyListItem} on:update={updateListItem} listId={list._id}/>
              {/if}
            {:else}
              While the list has a date attached, no list items in this list with date assigned.
            {/if}
          {:else}
            While the list has a date attached, no list items in this list with date assigned.!!!
          {/if}


         

        </section>

      {/each}
    {:else}
      <section>
        <p>You currently have no lists.</p>
        <p>Go to the <a href="#/lists">Lists</a> view and assign some due dates to see some content here.</p>
      </section>
    {/if}


  </section>
</PageContent>
<style lang="sass">
  
  hr
    margin-bottom: 8px
    
  section[radar]
    margin-bottom: 36px

  ul[slot="nav"]
    margin-top: 100px

    padding-left: 32px
    a
      cursor: pointer
    li
      padding: 4px 0px

  header
    h1
      font-size: 36px
      color: var(--darkest-blue)
    margin-bottom: var(--spacing-m)


  [horizon-label]
    margin-top: 18px
    margin-bottom: 36px
    width: 100%
    p
      margin: 0px 24px
      color: var(--darker-gray)
      text-align: center

  // RADAR


  [collapsible], [menu], [progress] // set cell wdiths
    width: 32px


  .radar-list
    max-height: 42px



    //border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 96px)
    max-width: calc(100% + 96px)
    margin-left: -64px // 24+8px for icon cols

    margin-bottom: 8px



    [menu]
      text-align: center


    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [visible-on-hover]
      svg
        path
          fill: transparent

    &:hover
      background: var(--list-row-highlight)
      [visible-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)









</style>
