  dispatch = createEventDispatcher()

  syncing = undefined

  subscribe = PouchIsSyncing.subscribe (val)->
    syncing = val


  `$: current = $location.split("/")[1]`
  `$: rotate = syncing ? "rotate" : null`

</script>

<ul>

