# What Is This App

This application is a list management tool to help list minded people track work. 

## This App is a Beta

That means there are lots of bugs still, and there is potential to lose data. Don't use this as the only copy of important data. If you choose to do so, make sure you back up your data frequently from the settings.

## Views

### Radar

The Radar view is like real-world radar. It lets you see items (before and after today) using a time horizon. For example, if your time horizon is 7 days, you can see items with dates starting 7 days ago, ending 7 days from now.

### Favorites

Favorites are lists that have been marked favorites, whether they are current or archived.

### Lists

Lists shows only current lists (i.e., items that have not been archived).

### Archived

Archived lists show only archived lists.

### Sync

*This is in development, it is not recommended you mess with this right now.*

### Settings

Settings are some settings and tools realted to the app. 

Since this app is in beta, it's recommended that you "Export All Data" frequently so that you don't lose data.

Many of the features in the Settings are unpolished, so use the features judiciously.



### Help

This page is the help!


# Tips


* Drag and Drop to nest and reorder. <b>It's not working perfectly yet.</b>
* If you hover between two list items, you'll notice a divider will appear. If you click on it, it will let you insert a new item there.
* List items have a menu on the right side that let you do stuff.
* Radar shows items that have a date assigned to it
* Ongoing is a placeholder right now. List items will have an "ongoing" flag on them soon, and this view filters out ongoing items.
* Sync is still experimental and under development, so no instructions on it.
* If an item has a detected URL (http, https, file, mailto), in the menu, you can copy or open the first detected URL.
* If you want to obfuscate the text for an item, you can hide it from the menu


