<script lang="coffeescript">
  import { fade, fly } from 'svelte/transition'
  import { afterUpdate } from "svelte"

  export show = false
  export message = "Flies in flies out"
  export delay = 6

  hide = ()->
    show = false
    #console.log "HIDE: #{show}"

  debouncedHide = hide.debounce((delay).sToMilliseconds())

  afterUpdate ()->
    #console.log "update #{show}"
    if show
      debouncedHide()
</script>

<style lang="sass">
  div
    background: var(--darkest-gray)
    color: var(--white)
    padding: 8px 16px
    position: fixed
    bottom: 16px
    margin: 0 auto
    border-radius: 6px
    max-width: 300px
</style>

{#if show}
	<div in:fly="{{ y: 200, duration: 1000 }}" out:fly="{{ y: 200, duration: 1000 }}" {message}>
		<slot>{@html message}</slot>
	</div>
{/if}
