<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()

  
  import {IconProgressCircle, TippyPopover} from "../../../Braintapper"
  import { debug, FormatRelativeDate, IconSvg } from "../../../Braintapper"


  import DueDate from "./_DueDate.svelte"


  tippyObject = undefined
  component = undefined
  isShowing = false

  export value = Date.create()
  export style = undefined
  export item = undefined
  export isListItem = true
  export idAttribute = "id"

  isShowing = false


  newValue = undefined

  


  
  




  dueDateState = (item,date)->
    unless Object.has(item, "percent_complete")
      item["percent_complete"] = 0
    if date?
      if item.percent_complete == 100
        return "complete"
      else
        if Object.isDate(date)
          return date.comparedTo(Date.create())
        else
          return Date.create(date).comparedTo(Date.create())
    else
      return "before"

  `$: newValue = value`
  `$: state = dueDateState(item,newValue)`

  tippyComponent = undefined
  tippyObject = undefined

  clear = ()->
    tippyObject.hide()
    console.error "clear"
    newValue = null
    dispatch "close"
    eventObject = {  due_date: newValue }
    eventObject[idAttribute] = item[idAttribute]
    dispatch "update", eventObject
    tippyComponent.$set { dateValue: newValue }  

  setDate = (e)->
    console.error "set-date"
    value = e.detail
    console.log e.detail
    newValue = e.detail.format("{yyyy}-{MM}-{dd}")
    tippyComponent.$set { dateValue: newValue }
    eventObject = {  due_date: newValue  }
    eventObject[idAttribute] = item[idAttribute]
    dispatch "update", eventObject

    tippyObject.hide()  

  show = ()->
    isShowing = true
    tippyComponent.$set { isShowing: isShowing }

  hide = ()->
    isShowing = false
    tippyComponent.$set { isShowing: isShowing }

  initPopover = ()->
    tippyComponent.$set { dateValue: newValue, isShowing: isShowing }

  updatePopover = ()->
    tippyComponent.$set { dateValue: newValue }
</script>

<TippyPopover template={DueDate} on:setDate={setDate} on:clear={clear} on:show={show} on:hide={hide} on:create={initPopover} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} theme="datepicker">
  <div show-on-hover is-showing={isShowing} {style}>

      {#if newValue == null}
        <IconSvg icon="calendar"/>

      {:else}
        <a duedate {state} {isListItem}><em>
          <FormatRelativeDate value={newValue}/>
        </em></a>

      {/if}

  </div>
</TippyPopover>
<style global lang="sass">
  .tippy-box
    &[data-theme="datepicker"]

</style>
