// list
var List, ListItem, Lists;

List = require("./List.js");

ListItem = require("./ListItem.js");

Lists = (function() {
  class Lists {
    constructor() {}

    
    // OBJECT MANAGEMENT

    transformCollection(items) {
      var that;
      that = this;
      // console.table items
      return items.map(function(i) {
        //console.log JSON.stringify(i, null, 2)

        //console.log "new list"
        //console.log b
        return new List(i);
      });
    }

    
    // POUCHDB FUNCTIONS

    sync() {
      //console.log "pouchresourece.sync"
      return window.pouchSync();
    }

    select(selector, thenCb, catchCb, finallyCb) { // current only
      var that;
      console.log("SELECT CURRENT");
      that = this;
      if (selector == null) {
        selector = {
          doctype: "List",
          $or: [
            {
              is_archived: false
            }
          ]
        };
      }
      return db.createIndex({
        index: {
          fields: ["doctype", "list_id", "is_archived", "is_favorite"],
          name: "ListsFirstPass"
        }
      }).then(function(indexResult) {
        return db.find({
          selector: selector
        }).then(function(result) {
          that.items = that.transformCollection(result.docs);
          if (thenCb != null) {
            return thenCb(that.transformCollection(result.docs)); // that.items - dereferenced?
          }
        }).catch(function(err) {
          if (catchCb != null) {
            return catchCb(err);
          } else {
            console.log("select  err");
            return console.log(err);
          }
        }).finally(function() {
          if (finallyCb != null) {
            return finallyCb();
          }
        });
      });
    }

    selectAll(thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find({
        selector: {
          doctype: "List"
        }
      }).then(function(result) {
        if (thenCb != null) {
          that.items = that.transformCollection(result.docs);
          return thenCb(that.items);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    selectOne(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      // console.log "selectone #{id}"
      return db.get(id).then(function(doc) {
        var rootItem;
        //console.log "select one"
        //console.log doc
        if (thenCb != null) {
          rootItem = new List;
          // consider removing transformation
          return thenCb(rootItem.transformModel(doc));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("select on e err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    find(options, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      return db.find(options).then(function(result) {
        if (thenCb != null) {
          return thenCb(that.transformCollection(result.docs));
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("find  err");
          return console.log(err);
        }
      }).finally(function() {
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

    transformBeforeSave(document) {
      // save safe object

      return document;
    }

    insert(obj, thenCb, catchCb, finallyCb) {
      var newRecord, that, writeRecord;
      that = this;
      newRecord = new List(obj);
      // set ordinal based on list length
      newRecord.index = that.items.length;
      //console.log "newRecord"
      //console.log newRecord
      newRecord.created_at = Date.create();
      //newRecord = @transformBeforeSave(newRecord)

      // todo: guid consistency check first
      writeRecord = newRecord.pouchify();
      // delete writeRecord.id
      // bdelete writeRecord._id
      writeRecord["_id"] = writeRecord.id;
      console.debug(writeRecord);
      return db.put(writeRecord).then(function(response) {
        // note the response is this format:
        // ok: true
        // id: id
        // rev: rev_id
        if (thenCb != null) {
          return thenCb(response);
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("insert.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("insert finally");
        that.sync();
        if (finallyCb != null) {
          console.log("insert.finally");
          return finallyCb();
        }
      });
    }

    destroy(id, thenCb, catchCb, finallyCb) {
      var that;
      that = this;
      console.error(`Destroy from list ${id}`);
      return db.find({
        selector: {
          $or: [
            {
              "id": id
            },
            {
              "list_id": id
            }
          ]
        }
      }).then(function(response) {
        var refreshedCb, removedCb;
        if (thenCb != null) {
          refreshedCb = function() { // execute then cb
            return thenCb(that.items);
          };
          removedCb = function() { // refresh @ items
            // that.sync()
            return refreshedCb();
          };
          //that.select refreshedCb, catchCb, finallyCb
          console.log("destroy response docs");
          console.log(response.docs);
          response.docs.forEach(function(doc) {
            return doc["_deleted"] = true;
          });
          
          // db.remove docToRemove, removedCb
          return db.bulkDocs(response.docs).then(removedCb).catch(function(err) {
            if (catchCb != null) {
              return catchCb(err);
            } else {
              console.error("RootItem.destroy");
              return console.debug(err);
            }
          });
        }
      }).catch(function(err) {
        if (catchCb != null) {
          return catchCb(err);
        } else {
          console.log("destroy.catch");
          return console.log(err);
        }
      }).finally(function() {
        console.log("destroy finally");
        that.sync();
        if (finallyCb != null) {
          return finallyCb();
        }
      });
    }

  };

  Lists.prototype.name = "Root Item List";

  Lists.prototype.doctype = "List";

  Lists.prototype.version = 1;

  Lists.prototype.items = [];

  return Lists;

}).call(this);

module.exports = Lists;
