<script lang="coffeescript">




  import FileSaver from "file-saver"
  import md5 from "md5"


  # Data Items

  import { 
    debug,
    Button,
    FileInput
    InlineInput,
    InlinePassword,
    Checkbox 
  } from "../../Braintapper"

  import { 
    List
    ListItem
    PageContent
    PageHeader 
  } from "../../Application"



  


  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()


  export files = undefined

  export fileObj =  {}

  export jsonFile = undefined



  `$: if (files) { fileObj = files }`

  # resource = new RootItemList()

  replicationEnabled = localStorage.getItem("radar_sync")
  replicationDb = localStorage.getItem("radar_remote_db")
  replicationRequiresAuthentication = localStorage.getItem("sync_authentication") || "false"


  toggleSync = ()->
    localStorage.setItem "radar_sync"
    !(replicationEnabled == 'true')
    replicationEnabled = localStorage.getItem("radar_sync")


  importData = ()->
    console.error "importData"
    console.debug files
    if files?
      console.log "file info"
      fileArray = Array.from(files)
      console.debug fileArray.first()
      reader = new FileReader()
      reader.onloadend = (response)->
        console.log "READ FILE"
        console.log response
        jsonFile  = response.target.result

        jsonToData = JSON.parse(jsonFile).map (o)->
          delete o._rev
          return o


        jsonToData.forEach (item)->
          #item.id = uuid()
          item["_id"] = item.id
          # item.name = "#{item.name} imported at #{Date.create()}"
        
        db.bulkDocs(jsonToData)
        .then (result)->
          console.error "imported"
          console.debug result


      reader.readAsText fileArray.first()

  updateDb = (e)->
    console.log e

  updateUser = (e)->
    console.log e

  updatePassword = (e)->
    console.log e

  saveAs = ()->

    # full export
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      intermediate = result.rows.map (row)->
        return row.doc

      console.log "Intermediate items: #{intermediate.length}"
      output = []
      orphanedItems = []
      lists = intermediate.filter (item)->
        delete item._rev
        delete item._id
        return item.doctype == "List"



      lists.sortBy "name"

      console.log "LISTS #{lists.length}"
      console.log lists


      
      
      listItems = intermediate.filter (item)->
        return item.doctype == "ListItem"

      others = intermediate.filter (item)->
        test = false
        if item.doctype?
          if !["List","ListItem"].includes(item.doctype)
            test = true
        else
          if item.id?
            test = true
          
        return test

      orphanedItems.append others


      console.log  "----------- process list items #{listItems.length}"

      console.log "others: #{others.length}"

      console.log listItems
      console.log others
      
      groupedListItems = listItems.groupBy "list_id"

      # console.debug groupedListItems
      
      lists.forEach (list, index)->
        #console.log "LIST"
        #console.log list
        output.append list
        if index < 2
          console.debug output
        if groupedListItems[list.id]?
          output.append groupedListItems[list.id].sortBy (listItem)->
            return listItem.ancestry.join " "
        else
          orphanedItems.append groupedListItems[list.id]

      output.append orphanedItems.map (o)->
        o["list_id"] = "orphan"
        return o

      #console.table output.map (o)->
      #  return {
      #    id: o.id
      #    doctype: o.doctype
      #    name: o.name
      #    list_id: o.list_id 
      #  }

      jsonString = JSON.stringify(output,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})

      FileSaver.saveAs blob, "braintapper-radar-archive-#{Date.create().toTimestampId()}.json"
      




  forceSync = ()->
    console.log remoteDb
    if remoteDb?
      console.log "syncing to remote db"
      db.sync( remoteDb, {retry: true} )
      .on 'complete', ()->
        console.log "complete!"
      .on 'change', (change) ->
        # yo, something changed!
        console.log "sync change"
        console.log change
      .on 'paused', (info) ->
        # replication was paused, usually because of a lost connection
        console.log "sync paused"
        console.log info
      .on 'active', (info) ->
        # replication was resumed
        console.log "sync active"
        console.log info
      .on 'error', (err) ->
        console.log "sync error"
        console.log err


  restore = ()->

  destroy = ()->

    db.destroy()
    .then (response)->
      console.log "destroyed"
      console.debug response
      window.createPouchDb()
      window.mounted()
    .catch (err)->
      console.error "destroy error"
      console.log err

    ###
    # doesn''t wipe deleted docs from db
    db.allDocs({ include_docs: false })
    .then (response)->
      console.error "DESTROY ALL DOCS"
      docsToBeDeleted = response.rows.map (o)->

        return {
          _id: o.id
          _rev: o.value.rev
          _deleted: true
        }
      console.debug docsToBeDeleted
      db.bulkDocs docsToBeDeleted
      .then (result)->
        console.error "Attempt to delete all docs"
        console.log result
        console.error "COMPACT"
        db.compact()
        .then (result)->
          console.error "compacted"
          console.debug result
    ###


  notificationPermssionRequest = ()->
    Notification.requestPermission().then (permission) ->
      # If the user accepts, let''s create a notification
      if permission == 'granted'
        notification = new Notification('Hi there!')
      else
        console.log "not granted"
      return

  if electron
    edition = "Desktop"
  else
    edition = "Web"
  `$: disableReplication = (replicationEnabled != 'true')`


  # resource
  cleanLists = ()->
    db.find({selector: {doctype: "List"}}).then (result)->
      db.bulkDocs result.docs.map (doc)->
        doc["_deleted"] = true
        return doc
      .then  (result)->
        console.error "Lists deleted"

  cleanListItems = ()->
    db.find({selector: {doctype: "ListItem"}}).then (result)->
      db.bulkDocs result.docs.map (doc)->
        doc["_deleted"] = true
        return doc
      .then  (result)->
        console.error "cleanListItems deleted"

  compact = ()->
    db.compact()
    .then (result)->
      console.log "COMPACTED"
      console.log result

  migrate = ()->
    console.error "start migration"
    staging = []
    output = []
    migrateScb = (docs)->
      
      docs.forEach (doc)->
        console.log "doc: #{doc.name}, list_items: #{doc.list_items.length}"
        
        # doc.migrate(staging)


      console.table staging

      
      staging.forEach (item)->

        if item.type == "List"
          newModel = new List(item)
          
        else
          newModel = new ListItem(item)
          

        output.append newModel.pouchify()



      jsonString = JSON.stringify(output,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})
      # note - the md5 works on the string loaded in the file, which is not the same as the file''s md5
      # use a different handler for electron
      FileSaver.saveAs blob, "braintapper-radar-migrated-#{Date.create().toTimestampId()}--#{md5(jsonString)}.json"


      
      db.bulkDocs(output)
      .then (result)->
        console.error "imported new lists and list items"
        console.debug result


    resource.selectAll migrateScb


  findBadDocTypes = ()->
    db.find 
      selector: 
        doctype: 
          $exists: false

    .then (result)->
      console.debug result
      ids = result.docs.map (o)->
        return o.id
      db.find
        selector:
          list_id:
            $in: ids
      .then (subresult)->
        console.log "orphaned list items"
        console.log subresult



  forceFix = ()->
    db.find(
        {
          selector: {
            $or: [
              {
                "id": "9a219ca9-410d-4bc6-acd2-52fe86fbc112"
              }

            ]
          }
        }
        ).then (response)->
          
          console.table response.docs        

      .catch (err)->
        if catchCb?
          catchCb err
        else
          console.log "destroy.catch"
          console.log err
      .finally ()->
        console.log "destroy finally"
        
        if finallyCb?

          finallyCb()



  fixListsWithMismatchedIds = ()->
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      mismatched = result.rows.filter (o)->
        
        return (o.doc._id != o.doc.id) && (!o.doc._id.startsWith("_"))
      mismatchedDocs = mismatched.map (o)->
        return o.doc

      console.table mismatchedDocs

      mismatchedIds = mismatchedDocs.map (o)->
        return o.id

      console.debug mismatchedIds
      db.find {
        selector:
          list_id:
            $in: mismatchedIds # remove dupes

      }
      .then (secondaryResult)->
        mismatchedListItems = secondaryResult.docs.sortBy("list_id")

        mismatchedListMap = mismatchedListItems.groupBy("list_id")
        console.log mismatchedListMap

        changes = []

        Object.keys(mismatchedListMap).forEach (key)->
          found = mismatchedDocs.find (o)->
            return o.id = key
          changes.append { _id: found._id, id: found._id, doctype: "List", old_id: found.id }
          changes.append mismatchedListMap[key].map (o)->
            return {_id: o._id, list_id: key }

        console.table changes
        console.log changes.map("_id").unique()

        db.find {
          selector:
            _id:
              $in: changes.map("_id")
        }
        .then (result)->
          console.log "changedocs"
          console.debug result.docs.groupBy("doctype")


  deleteIndices = ()->

    db.getIndexes().then  (indexesResult) ->
      indexesResult.indexes.forEach (index) =>
        if (index.name != "_all_docs") 
          console.log(index)
          db.deleteIndex(index)



  rawDump = ()->

    # full export
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      intermediate = result.rows.map (row)->
        return row.doc


      jsonString = JSON.stringify(intermediate,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})

      FileSaver.saveAs blob, "braintapper-radar-dump-#{Date.create().toTimestampId()}.json"
      


  saveAs = ()->

    # full export
    db.allDocs {
      include_docs: true
      attachments: false
    }
    .then (result)->
      intermediate = result.rows.map (row)->
        return row.doc

      console.log "Intermediate items: #{intermediate.length}"
      output = []
      orphanedItems = []
      lists = intermediate.filter (item)->
        delete item._rev
        delete item._id
        return item.doctype == "List"



      lists.sortBy "name"

      console.log "LISTS #{lists.length}"
      console.log lists


      
      
      listItems = intermediate.filter (item)->
        return item.doctype == "ListItem"

      others = intermediate.filter (item)->
        test = false
        if item.doctype?
          if !["List","ListItem"].includes(item.doctype)
            test = true
        else
          if item.id?
            test = true
          
        return test

      orphanedItems.append others


      console.log  "----------- process list items #{listItems.length}"

      console.log "others: #{others.length}"

      console.log listItems
      console.log others
      
      groupedListItems = listItems.groupBy "list_id"

      # console.debug groupedListItems
      
      lists.forEach (list, index)->
        #console.log "LIST"
        #console.log list
        output.append list
        if index < 2
          console.debug output
        if groupedListItems[list.id]?
          output.append groupedListItems[list.id].sortBy (listItem)->
            return listItem.ancestry.join " "
        else
          orphanedItems.append groupedListItems[list.id]

      output.append orphanedItems.map (o)->
        o["list_id"] = "orphan"
        return o

      #console.table output.map (o)->
      #  return {
      #    id: o.id
      #    doctype: o.doctype
      #    name: o.name
      #    list_id: o.list_id 
      #  }

      jsonString = JSON.stringify(output,null,2)
      blob = new Blob([jsonString], {type:  "text/plain;charset=utf-8"})

      FileSaver.saveAs blob, "braintapper-radar-archive-#{Date.create().toTimestampId()}.json"
      


  restore = ()->

  destroy = ()->

    db.destroy()
    .then (response)->
      console.log "destroyed"
      console.debug response
      window.createPouchDb()
      window.mounted()
    .catch (err)->
      console.error "destroy error"
      console.log err

    ###
    # doesn''t wipe deleted docs from db
    db.allDocs({ include_docs: false })
    .then (response)->
      console.error "DESTROY ALL DOCS"
      docsToBeDeleted = response.rows.map (o)->

        return {
          _id: o.id
          _rev: o.value.rev
          _deleted: true
        }
      console.debug docsToBeDeleted
      db.bulkDocs docsToBeDeleted
      .then (result)->
        console.error "Attempt to delete all docs"
        console.log result
        console.error "COMPACT"
        db.compact()
        .then (result)->
          console.error "compacted"
          console.debug result
    ###

</script>

<svelte:head>
  <title>Settings</title>
</svelte:head>
<PageContent>
  <PageHeader><h1 title>Tools</h1></PageHeader>
  <article type="content">


    <h2>Database</h2>



    <h3>Raw Dump</h3>
    <p>This will save the contents of the entire database.</p>
    <Button on:click={rawDump}>Save Raw Dump to Disk</Button>



    <h3>Export Lists</h3>
    <p>This will save your collections as a JSON file into your downloads folder. This does not export your settings.</p>
    <Button on:click={saveAs}>Save to Disk</Button>

    <h3>Import</h3>
    <p>Import data from a JSON file. This will create new IDs for all incoming items, so if you are importing a backup, you may end up with items having duplicate names.</p>
    
    <h2>Data</h2>

    <h2> Warning, Items Below May Not Be Functional</h2>

    
    <FileInput bind:files/>



    <h3>Delete Indices</h3>
    <p>Delete PouchDB indices. This is non-destructive as indices will be recreated as required.</p>
    <Button state="critical" on:click={deleteIndices} disabled={false}>Delete Indices</Button>


    <h3>Danger Zone</h3>

    <h2>Restore</h2>
    <p>This will clear all existing data (including on any synchronized databases) and load the contents of an export file into your system. Your settings will remain intact.</p>
    <Button state="critical" on:click={restore} disabled={true}>Restore</Button>

    <h2>Delete All Local Data</h2>
    <p>This will delete your local database but not the remote database. It will disable sync settings and delete the remote address of your remote database.</p>
    <Button state="critical" on:click={destroy} disabled={false}>Delete</Button>

   


  </article>

</PageContent>
<style lang="sass">
  article
    margin: 0px

</style>
