<script lang="coffeescript">
  # todo: implement cancel
  import { tick, createEventDispatcher } from 'svelte'

  dispatch = createEventDispatcher()

  # todo: cancel

  # Props
  # export value = ''
  export style = undefined
  export inputStyle = undefined
  export type = 'text'
  export placeholder = ''
  export labelClasses = ''
  export inputClasses = ''

  export item = { id: "", name: "" }
  export inputAttribute = "name"
  export idAttribute = "id"


  export doubleClick = true

  export id = undefined
  export delay = 250


  export editing = false

  # todo: export selectAllOnEdit = false
  # todo: export forceEditMode = false

  inputEl = undefined
  label = undefined
  originalValue = undefined

  value = undefined


  # Computed
  # todo: clean these up

  `$: isText = type === 'text'`
  `$: isNumber = type === 'number'`
  `$: if (isNumber) {
        label = value === '' ? placeholder : value;
      } else if (isText) {
        label = value ? value : placeholder;
      }
  const toggle = async (_) => {
    editing = !editing;

    originalValue = value;
    if (editing) {
      await tick();
      inputEl.focus();
      inputEl.select();
      value = "" + item[inputAttribute];

    }
  }`
  handleInput = (e) ->
    if isNumber
      value = +e.target.value
    else
    value = e.target.value


  handleKeyup = (e) ->
    dispatch("keyup", value)
    switch e.keyCode
      when 13
        inputEl.blur()
        unchanged = (value == item[idAttribute])    
        unless unchanged && (value.length > 0)
          console.log "value: #{value}"

          eventObject = {
            id: item[idAttribute]
          }
          eventObject[inputAttribute] = value
          console.log "eventObject"
          console.log eventObject

          dispatch("update",eventObject)            

      when 27
        value = originalValue
        inputEl.blur()
    # }

  handleBlur = (_) ->

    toggle()



  # Click tracking

  clickCount = 0

  clickReset = ()->
    switch clickCount
      when 1
        single()
      when 2
        double()
      else
        clickCount = 0
    # }
  delayedReset = clickReset.debounce(delay)

  single = () ->
    clickCount = 0
    if !doubleClick
      toggle()
    dispatch "clicked", id

  double = () ->
    clickCount = 0
    if doubleClick
      toggle()
    dispatch "doubleclicked", id

  click = ()->
    clickCount++
    delayedReset()



</script>

{#if editing && (isText || isNumber)}
  <input
    class={inputClasses}
    style={inputStyle}
    bind:this={inputEl}
    {type}
    {value}
    {placeholder}
    on:click|stopPropagation
    on:input={handleInput}
    on:keyup={handleKeyup}
    on:blur={handleBlur}>
{:else}
  <div
    {id}
    class={labelClasses}
    {style}>
    <span on:click={click}><slot><span>{item[inputAttribute]}<span spacer/></span></slot></span>
  </div>
{/if}
<style lang="sass">

  input, div, div span

    font-family: inherit
    font-size: inherit
    font-weight: inherit
    border: 0px solid transparent
    padding: 0px

  input
    width: 100%
    color: var(--dark-red)
  input::selection
    background: var(--dark-red)
    color: white
  div
    // padding: var(--fine-spacing-xs) var(--fine-spacing-m)
    min-height: var(--font-xl)
    border: 1px solid transparent



</style>