<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  import { slide } from "svelte/transition"
  import { sineIn, sineOut } from 'svelte/easing'
  
  dispatch = createEventDispatcher()
  
  import {
    FlexRow
    FlexRowCell
  } from "braintapper-svelte-flex"

  import { 
    debug
    Indent
    IconSvg 
    TippyContextMenu
  } from "../../../Braintapper"
  
  import Menu from "./_DividerMenu.svelte"


  export index = undefined
  export indentation = 0
  export item = { is_divider: true }
  export listId = undefined
  export firstItem = false

  export delay = 250 # click
  export dragState = false

  isOver = false
  isHover = false
  isInvalid = false

  export isExpanded = false

  over = (e)->
    e.preventDefault()
    e.stopPropagation()
    isOver = true

    isInvalid = false

    # console.error "OVER"

    #if isInvalid
    #  return e.preventDefault()
    #console.log "return is invalid: #{!isInvalid}"
    # return !isInvalid

  hover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      isHover = true
      # console.error "HOVER"

  unhover = (e)->
    e.preventDefault()
    e.stopPropagation()
    unless isOver
      # console.log "UNHOVER"
      isHover = false
      collapse()

  leave = (e)->
    draggable = false
    isOver = false
    isInvalid = false
    # console.error "LEAVE"
    # isHover = false

  add = (name)->
    console.log "add: index: #{item.index}"
    toIndex = item.index

    isHover = false
    isExpanded = false
    console.error "divider add"
    console.debug { ancestry: item.ancestry, index: toIndex }
    if name?
      dispatch "add", { ancestry: item.ancestry, index: toIndex, name: name }
    else
      dispatch "add", { ancestry: item.ancestry, index: toIndex }



  toggleExpanded = ()->
    isExpanded = !isExpanded

  collapse = (e)->
    # console.log "collapse"
    if e?
      e.stopPropagation()
    isExpanded = false


  # Click tracking

  clickCount = 0

  clickReset = ()->
    switch clickCount
      when 1
        single()
      when 2
        double()
      else
        clickCount = 0
    # }
  delayedReset = clickReset.debounce(delay)



  single = () ->
    clickCount = 0
    # toggleExpanded()
    # collapse.delay(5000)
    

    #dispatch "clicked", id

  double = () ->
    clickCount = 0
    # add()
    #dispatch "doubleclicked", id

  click = (e)->
    e.stopPropagation
    clickCount++
    delayedReset()



  ###

  Drop Cases

  1. Drop to Reorder

  2. Drop to Nest and Reorder


  If target ancestry is the same as origin, then it''s a reorder 

  If target ancestry is different from origin, then it''s a nested move

  ###

  drop = (e)->
    isOver = false
    isInvalid = false
    draggable = false


    eventType = "drop"
  

    ancestry = item.ancestry.clone()

    # ancestry.pop()
    
    #console.log "NestableDivider.drop item"
    #console.debug item
    dropItem = item 
    # { ancestry: ancestry, index: item.index }

    console.log "dropItem"
    console.log dropItem
    console.log e.detail


    #console.log "NestableDivider.svelte"

    e.preventDefault()
    dragItem = JSON.parse(e.dataTransfer.getData("text/plain"))

    #console.log item

    #console.log "--------------------------------"

    unless firstItem
      if (item.list_items || []).length > 0
        #console.log "------------------------------ DIVIDER DROP"
        #console.log "dragitem"
        #console.debug dragItem
        #console.log "dropitem"
        #console.log item


        #console.log "(item.list_items || []).length #{(item.list_items || []).length} :: nest"
        eventType = "drop"
        item["to_index"] = 0
      else

        console.log "DROP ITEM #{item.name} index: #{item.index}"
        item["to_index"] = item.index + 1

      # console.log "evntType: #{eventType}"

      dropped = true

      unless Object.has(item, "is_divider")
        item["is_divider"] = true

      #console.log "DroppableDivider - drop"
      #console.log item

    else # handle first item
      console.error "FRIST ITEM"
      item["to_index"] = 0

      console.log dragItem
      console.log "eventType #{eventType}"
      dropped = true

    if dropped
      dispatch "drop",
        divider: true
        dragItem: dragItem
        dragIndex: dragItem.index
        dragListId: dragItem.listId
        dropItem: dropItem # item
        dropIndex: index + 1
        dropListId: listId


  
  tippyComponent = undefined
  tippyObject = undefined

  menuSelect = (e)->
    switch e.detail
      when "add"
        add()
      when "add-clipboard"
        console.log "added from clipboards"
        navigator.clipboard.readText().then (o)->
          add o



</script>

<div
  divider
  class:over={isOver}
  class:hover={isHover}
  is-dragging={dragState}
  on:drop={drop}
  on:dragover={over}
  on:dragleave={leave}
  on:mouseenter={hover}
  on:mouseleave={unhover}
  class:expanded={isExpanded ? "expanded" : false}
  index={item.index}
>
  <FlexRow>
  <FlexRowCell flex="initial">
    <Indent level={indentation} offset={24} flex="initial"/>
  </FlexRowCell>
  <FlexRowCell flex>
    <TippyContextMenu template={Menu} bind:tippyComponent={tippyComponent} bind:tippyObject={tippyObject} on:select={menuSelect}>
    <div 
      class:expanded={isExpanded ? "expanded" : false}
      class:over={isOver}
      
      class="indented"
    >
   <!--  {item.index} :: ancestry {JSON.stringify(item.ancestry)}  -->
    
    {#if isExpanded}
      <div class="add-divider"
        in:slide="{{delay: 150, duration: 300, easing: sineIn }}"
        class:invalid={isInvalid}
         
         layout="row"
      >


      </div>
    {/if}
    
      <div class="droptarget"
        class:invalid={isInvalid}>
          &nbsp;

          &nbsp;
      </div>
      </div>
    </TippyContextMenu>
  </FlexRowCell>

   
  </FlexRow>


</div>


<style lang="sass">



  div[divider]
    // margin: 2px 0px
    height: 8px
    // debug
    // height: 24px
    //transition: height 0.15s ease-out
    border-radius: 4px

    .indented
      border-radius: 4px
      background: transparent


    &:hover
      .indented
        background: var(--list-row-highlight)
    
    //&.over
    //  height: 24px
    //  transition: height 0.15s ease-in
    //  .indented
    //    background: #f1f1f1
    
    &.expanded
      height: 40px
      //transition: height 0.15s ease-in


    .expanded
      padding: 8px 0px
      border-top: 8px solid transparent
      border-bottom: 8px solid transparent

      height: 40px

      .indented
        background: transparent
    //background: var(--lightest-blue)


    // drag and drop
    .droptarget
      height: 8px
      color: transparent
      // line-height: 24px

    .indented.over
      .droptarget
        padding: 0px 8px
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px




    .add-divider
      text-align: left
      border-radius: 3px
      border: 0px
      padding: 0px 8px
      //line-height: 24px
      top: -6px
      vertical-align: middle
      button
        //line-height: 24px
        margin-right: 8px
      [icon]
        padding-right: 8px
        svg
          fill: var(--green)
        color: var(--green)
      button[cancel]
        margin-left: 18px     
        svg
          fill: var(--red)
        color: var(--red)           
      // background: var(--green) !important
    // hover state


    &[is-dragging="true"] //.dragging
      //height: 24px
      //transition: height 0.15s ease-in      
      //margin: 2px 0px
      
      //.indented
      //  background: #f1f1f1      
      // height: 24px
      // transition: height 0.15s ease-in
      // margin-bottom: 2px
      .indented
        background: #f1f1f1
      .droptarget
        padding: 0px 8px 
        color: var(--light-gray) !important
        background: transparent
        border-radius: 4px
        svg
          path
            fill: var(--light-gray)
      &.over
        .indented
          background: var(--gray)
          color: white



    &.invalid
      .placeholder
        background: var(--red) !important




    div[spacer] // accommodate expand collapse icon
      width: 24px

</style>
