<script lang="coffeescript">

  import { createEventDispatcher } from "svelte"
  dispatch = createEventDispatcher()
  import {

    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  
  import { IconProgressCircle } from "../../../Braintapper"


  # --

  export progressValue = { percent_complete: 0, color: "light-gray" }

  # --

  select = (val, color)->
    newValue = { percent_complete: val , color: color }
    dispatch "tippy-event", { event: "setProgress", value: newValue }


</script>



<div>
{#each ["green","yellow","red","blue","dark-gray","gray","light-gray"] as color}
  <FlexRow style="padding: 8px">
    {#each [0,25,50,75,100] as percentage}
      <FlexRowCell>
        <button type="button" on:click={select(percentage,color)} icon style="margin: 0px 2px">
          <IconProgressCircle value={percentage} {color}/>
        </button>
      </FlexRowCell>
    {/each}
  </FlexRow>
{/each}
</div>


<style lang="sass">
  div
    background: white

</style>