<script lang="coffeescript">

  import AddStub from "./_AddStub.svelte"
  import {createEventDispatcher} from "svelte"
  dispatch = createEventDispatcher()

  export showForm = false
  export stubPlaceholder = "Add item"
  export placeholder = "Enter name here."
  export attribute = "name"
  export isTextArea = false
  import {
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"


  import { TextAreaAutogrow,  ButtonIcon, IconSvg } from '../../../Braintapper'

  stub = {}
  stub[attribute] = ""

  resetStub = ()->
    stub[attribute] = ""

  edit = ()->
    showForm = true

  cancel = ()->
    showForm = false

  add = ()->
    if stub[attribute]?
      if stub[attribute].length > 0
        dispatch "add", stub
        resetStub()

  validate = (val)->
    if val?
      return (val.length > 0)
    else
     false
  keyup = (e)->
    if (e.key == "Return") || (e.key == "Enter")
      add()
    if (e.key == "Escape")
      cancel()

  textareakeyup = (e)->

    if (e.detail.key == "Escape")
      cancel()

  `$: invalid = !validate(stub[attribute])`

</script>

{#if !showForm}
  <AddStub {stubPlaceholder} on:edit={edit}/>
{:else}
  <FlexRow>

    <FlexRowCell flex>
      {#if isTextArea}
        <TextAreaAutogrow {placeholder} value={stub[attribute]} on:keyup={textareakeyup} autofocus style="width: 100%"/>
      {:else}
        <input type="text" {placeholder} bind:value={stub[attribute]} on:keyup={keyup} autofocus/>
      {/if}
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <ButtonIcon on:click={add} disabled={invalid}>
        <IconSvg icon="add"/>
      </ButtonIcon>
    </FlexRowCell>
    <FlexRowCell flex="initial">
      <ButtonIcon on:click={cancel}>
        <IconSvg icon="clear"/>
      </ButtonIcon>
    </FlexRowCell>
  </FlexRow>

{/if}
