<script lang="coffeescript">
  import { onMount, createEventDispatcher } from 'svelte'
  export value = undefined
  export style = undefined
  export classes = undefined
  export id = undefined
  export placeholder = undefined
  dispatch = createEventDispatcher()
  textarea = undefined


  change = (e)->
    textarea.style.height = "" # /* Reset the height*/
    textarea.style.height = (textarea.scrollHeight + 40) + "px"#

  keyup = (e)->
    dispatch("keyup", value)
    change()
    if (e.keyCode == 27)
      textarea.blur()

  input = (e)->
    dispatch "input", value

  blur = (e)->
    dispatch "blur", value

  onMount change

</script>

<style lang="sass">

  textarea
    height: auto

</style>


<textarea bind:this={textarea} bind:value on:change={change} on:keyup={keyup} {style} class={classes} {id}
  {placeholder}

    on:input={input}

    on:blur={blur}

></textarea>
