<script lang="coffeescript">


  #//  _     _     _         ____  _                   
  #// | |   (_)___| |_ ___  / ___|| |__   _____      __
  #// | |   | / __| __/ __| \___ \| '_ \ / _ \ \ /\ / /
  #// | |___| \__ \ |_\__ \_ ___) | | | | (_) \ V  V / 
  #// |_____|_|___/\__|___(_)____/|_| |_|\___/ \_/\_/  
                                                    

  import { onMount } from "svelte"
  import {push, pop, replace, location} from 'svelte-spa-router'
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"

  import { 
    debug
    InlineInput 
  } from "../../Braintapper"
  
  import { 
    PouchLastSyncResponse
    List
    ListItem
    PageContent
    PageHeader
    QuickEntry
    DueDatePopover
    ProgressPopover
    ListMenu
    ListDroppableDivider 
  } from "../../Application"

  debug.on = window.debug
  

  import ListItems from './_ListItems.svelte'


  
  export params = {}
  export list = undefined

  


  #//      _                    _              _ ____                    
  #//   __| |_ __ __ _  __ _   / \   _ __   __| |  _ \ _ __ ___  _ __    
  #//  / _` | '__/ _` |/ _` | / _ \ | '_ \ / _` | | | | '__/ _ \| '_ \   
  #// | (_| | | | (_| | (_| |/ ___ \| | | | (_| | |_| | | | (_) | |_) |  
  #//  \__,_|_|  \__,_|\__, /_/   \_\_| |_|\__,_|____/|_|  \___/| .__/   
  #//                  |___/                                    |_|      

  isDragging = false # global state

  setDragging = ()->
    #console.log "Show - dragging"
    isDragging = true

  unsetDragging = ()->
    #console.log "Show - stop dragging"
    isDragging = false




  #//             _   _     _     _   
  #//   __ _  ___| |_| |   (_)___| |_ 
  #//  / _` |/ _ \ __| |   | / __| __|
  #// | (_| |  __/ |_| |___| \__ \ |_ 
  #//  \__, |\___|\__|_____|_|___/\__|
  #//  |___/                          

  listModel = new List

  getList = ()-> # get list from db and refresh local view

    console.warn "getList fired"
    
    refreshScb = ()->
      #console.error "refreshScb fired, list should update"
      list = list
      window.currentList = list
      # console.debug list

    scb = (listObject) ->
      console.warn "getList scb"
      #console.debug listObject
      list = listObject
      # window.showlist = list
      list.treeify()

    listModel.get(params.id, scb, null, null)



  #//        _                  _     _     _   
  #//    ___| | ___  _ __   ___| |   (_)___| |_ 
  #//   / __| |/ _ \| '_ \ / _ \ |   | / __| __|
  #//  | (__| | (_) | | | |  __/ |___| \__ \ |_ 
  #//   \___|_|\___/|_| |_|\___|_____|_|___/\__|
  #//  


  cloneList = (e)->
    console.error "cloneList"

    scb = (response)->
      console.error "cloneList.scb"
      console.log response
      push "/lists/#{response}"

    console.error "clone listmodel"
    console.debug listModel
    listModel.clone(scb)
  


  #//                  _       _       _     _     _      _   _   _        _ _           _       
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_   / \ | |_| |_ _ __(_) |__  _   _| |_ ___ 
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __| / _ \| __| __| '__| | '_ \| | | | __/ _ \
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ / ___ \ |_| |_| |  | | |_) | |_| | ||  __/
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__/_/   \_\__|\__|_|  |_|_.__/ \__,_|\__\___|
  #//       |_|                                                                                  


  updateListAttribute = (e)->
    console.error "updateListAttribute"
    console.debug e.detail
    changes = e.detail
    scb = ()->
      Object.keys(changes).forEach (key)->
        listModel[key] = changes[key]
      list = list

    listModel.patch e.detail, scb



  #//                  _       _       _     _     _   ___ _                 
  #//  _   _ _ __   __| | __ _| |_ ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #// | | | | '_ \ / _` |/ _` | __/ _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | |_| | |_) | (_| | (_| | ||  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_| .__/ \__,_|\__,_|\__\___|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//       |_|                                                              

  updateListItem = (e)-> # patch a child in the list_items hierarchy
    console.error "List.Show.svelte: updateListItem"
    console.log e.detail

    found = theList.list_items_raw.find { id: e.detail.id }

    if found?
      if Object.has(e.detail,"is_expanded")
        list = list
        
        # don't refresh to save time
        found.patch e.detail 
      else
        found.patch e.detail, getList
    else
      console.error "item not found"



                                                            
  #//            _     _ _     _     _   ___ _               _____     ____             _     
  #//   __ _  __| | __| | |   (_)___| |_|_ _| |_ ___ _ __ __|_   _|__ |  _ \ ___   ___ | |_   
  #//  / _` |/ _` |/ _` | |   | / __| __|| || __/ _ \ '_ ` _ \| |/ _ \| |_) / _ \ / _ \| __|  
  #// | (_| | (_| | (_| | |___| \__ \ |_ | || ||  __/ | | | | | | (_) |  _ < (_) | (_) | |_   
  #//  \__,_|\__,_|\__,_|_____|_|___/\__|___|\__\___|_| |_| |_|_|\___/|_| \_\___/ \___/ \__|  
                                                                                           

  addListItemToRoot = (event)-> # add to list root
    console.log "addListItemToRoot"
    
    newListItem = new ListItem { list_id: list.id, name: event.detail.name, index: maxIndex(list) }
    
    saveCallback = ()->
      list.list_items_raw.append newListItem
      getList()

    newListItem.save saveCallback




  #//            _     _ _     _     _   ___ _                 
  #//   __ _  __| | __| | |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #//  / _` |/ _` |/ _` | |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | (_| | (_| | (_| | |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_|\__,_|\__,_|_____|_|___/\__|___|\__\___|_| |_| |_|
  #//                                                          

  addListItem = (event)->

    console.log "addListItem"
    console.log event.detail

    newListItem = new ListItem { list_id: list.id, id: uuid(), name: event.detail.name || "New item created at #{Date.create().format('{HH}:{mm}:{ss}')}", ancestry: event.detail.ancestry, index: event.detail.index }

    newListItem.list_reference = listModel

    scb = ()->
      listModel.list_items_raw.append newListItem
      listModel.treeify()
      getList()

    newListItem.save scb


  #//      _           _                   _     _     _   ___ _                 
  #//   __| | ___  ___| |_ _ __ ___  _   _| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  #//  / _` |/ _ \/ __| __| '__/ _ \| | | | |   | / __| __|| || __/ _ \ '_ ` _ \ 
  #// | (_| |  __/\__ \ |_| | | (_) | |_| | |___| \__ \ |_ | || ||  __/ | | | | |
  #//  \__,_|\___||___/\__|_|  \___/ \__, |_____|_|___/\__|___|\__\___|_| |_| |_|
  #//                                |___/                                       

  destroyListItem = (e)->
    console.error "destroyListItem"
    
    listItem = e.detail

    listItem.delete getList



  # //       _                  _     _     _   ___ _                 
  # //   ___| | ___  _ __   ___| |   (_)___| |_|_ _| |_ ___ _ __ ___  
  # //  / __| |/ _ \| '_ \ / _ \ |   | / __| __|| || __/ _ \ '_ ` _ \ 
  # // | (__| | (_) | | | |  __/ |___| \__ \ |_ | || ||  __/ | | | | |
  # //  \___|_|\___/|_| |_|\___|_____|_|___/\__|___|\__\___|_| |_| |_|


  cloneListItem = (e, scb)->

    found = theList.list_items_raw.find { id: e.detail.id }

    if found?
      siblings = theList.list_items_raw.filter (o)->
        o.ancestry.isEqual(found.ancestry)

      maxIndex = siblings.max("index")

      cloneOptions = 
        index: maxIndex.index + 1000

      found.clone cloneOptions, getList



   #//      _           _                   _     _     _   
   #//   __| | ___  ___| |_ _ __ ___  _   _| |   (_)___| |_ 
   #//  / _` |/ _ \/ __| __| '__/ _ \| | | | |   | / __| __|
   #// | (_| |  __/\__ \ |_| | | (_) | |_| | |___| \__ \ |_ 
   #//  \__,_|\___||___/\__|_|  \___/ \__, |_____|_|___/\__|
   #//                                |___/                 

  destroyList = (e)->
    console.error "DESOTRY LIST"
    scb = ()->
      push "/lists"
    listModel.delete scb


  
  #

  # Drag Drop

  #
  #      _ _       _     _           ____                  _   _                 _ _           
  #   __| (_)_   _(_) __| | ___ _ __|  _ \ _ __ ___  _ __ | | | | __ _ _ __   __| | | ___ _ __ 
  #  / _` | \ \ / / |/ _` |/ _ \ '__| | | | '__/ _ \| '_ \| |_| |/ _` | '_ \ / _` | |/ _ \ '__|
  # | (_| | |\ V /| | (_| |  __/ |  | |_| | | | (_) | |_) |  _  | (_| | | | | (_| | |  __/ |   
  #  \__,_|_| \_/ |_|\__,_|\___|_|  |____/|_|  \___/| .__/|_| |_|\__,_|_| |_|\__,_|_|\___|_|   
  #                                                 |_|                                        
  #

  dividerDropHandler = (event)->

    detail = event.detail

    affectedItems = list.list_items_raw.filter (item)->
      return (item.id == detail.dragItem.id) || item.ancestry.includes(detail.dragItem.id)

    nextInQueue = ()->
      if affectedItems.length > 0
        
        currentChange = affectedItems.shift()
        console.error "Processing #{currentChange.name}, #{affectedItems.length} items remaining"
        console.log currentChange
        if currentChange.id == detail.dragItem.id
          currentChange.patch {
            id: currentChange.id
            ancestry: detail.dropItem.ancestry
            index: detail.dropItem.index
          }, nextInQueue
        else
          
          currentChange.patch {
            id: currentChange.id
            ancestry: detail.dropItem.ancestry.clone().append(currentChange.ancestry.from(currentChange.ancestry.findIndex(detail.dragItem.id) ))
          }, nextInQueue
      else
        # done
        list.treeify()
        getList()

    nextInQueue()





  #//                  _   _   _                 _ _           
  #//  _ __   ___  ___| |_| | | | __ _ _ __   __| | | ___ _ __ 
  #// | '_ \ / _ \/ __| __| |_| |/ _` | '_ \ / _` | |/ _ \ '__|
  #// | | | |  __/\__ \ |_|  _  | (_| | | | | (_| | |  __/ |   
  #// |_| |_|\___||___/\__|_| |_|\__,_|_| |_|\__,_|_|\___|_|   
  #//                                                          


  nestHandler = (event)->

    ecb = (e)->
      console.log "nesthandler error"
      console.log e

    listModel.nest_item event.detail.dragItem, event.detail.dropItem, getList, ecb




  #//  ___           _           _             
  #// |_ _|_ __   __| | _____  _(_)_ __   __ _ 
  #//  | || '_ \ / _` |/ _ \ \/ / | '_ \ / _` |
  #//  | || | | | (_| |  __/>  <| | | | | (_| |
  #// |___|_| |_|\__,_|\___/_/\_\_|_| |_|\__, |
  #//                                    |___/ 


  minIndex = (list)->
    if Object.has(list,'list_items')
      min = list.list_items.min (o)->
        return o.index
      if min?
        min.index - 1000
      else
        - 1000
    else
      - 1000

  maxIndex = (list)->
    if Object.has(list,'list_items')
      max = list.list_items.max (o)->
        return o.index
      if max?
        max.index + 1000
      else
        1000
    else
      1000




  #//              __  __                   _   
  #//   ___  _ __ |  \/  | ___  _   _ _ __ | |_ 
  #//  / _ \| '_ \| |\/| |/ _ \| | | | '_ \| __|
  #// | (_) | | | | |  | | (_) | |_| | | | | |_ 
  #//  \___/|_| |_|_|  |_|\___/ \__,_|_| |_|\__|
  #//                                           


  onMount ()->
    PouchLastSyncResponse.subscribe (response)->
      unless response.handled
        console.error "PouchLastSyncResponse changed"
        console.log response      
        if response.direction == "pull"
          console.log "a pull occurred"
          pulledDocs = response.change.docs

          if pulledDocs.length > 0
            console.log "some docs were pulled, check to see if route was affected"
            affectedDocsForUrl = pulledDocs.filter (o)->
              return (o.list_id == list.id ) || (o.id == list.id)

            console.log "Affected docs = #{affectedDocsForUrl}"
            console.log affectedDocsForUrl      
            console.error "should refresh!"
            getList()
          else
            # No changed documents
            console.info "No sync changes related to this view"
          response.handled = true
          PouchLastSyncResponse.set response

        # todo make this smarter by handling edits, adds and deletions differently

  #//  ____                 _   _           
  #// |  _ \ ___  __ _  ___| |_(_)_   _____ 
  #// | |_) / _ \/ _` |/ __| __| \ \ / / _ \
  #// |  _ <  __/ (_| | (__| |_| |\ V /  __/
  #// |_| \_\___|\__,_|\___|\__|_| \_/ \___|
  #//                                       


  # handle a reroute
  `$: if (params.id) { getList() }`

  `$: title = list ? list.name : "Unnamed list"`
  `$: firstIndex = minIndex(list)`
  `$: lastIndex = maxIndex(list)`
  `$: theList = list`


</script>

<svelte:head>
  <title>Lists :: {title}</title>
</svelte:head>
{#if list}

  <PageContent>

    
    <PageHeader icon={true}>
      <div slot="icon"><ProgressPopover value={list.progress} item={list} on:update={updateListAttribute} size={36}/></div>
      <div>
        <FlexRow style="max-width: 100%;">
          <FlexRowCell flex style="overflow-x: hidden">
            <h1 title>
            <InlineInput item={list} on:update={updateListAttribute} style="margin-left: -2px" inputStyle="margin-top: 2px; margin-left: -1px"/>
            
            </h1>
          </FlexRowCell>
          <FlexRowCell style="padding-top: 16px; width: auto; margin-right: 8px" flex="initial">
            <DueDatePopover item={list} value={list.due_date} on:update={updateListAttribute} on:open on:close isListItem={false} idAttribute="id"/>
          </FlexRowCell>        
          <FlexRowCell style="padding-top: 16px; width: 32px" flex="initial">
            <ListMenu item={list} on:select={updateListAttribute} on:clone={cloneList} on:destroy={destroyList}/>
          </FlexRowCell>        
        </FlexRow>
      </div>
    </PageHeader>

    
    <hr/>
    
    
    <ListDroppableDivider item={{ ancestry: [], index: firstIndex, rootFirst: true} } on:drop={dividerDropHandler} on:add={addListItem} bind:dragState={isDragging}/>
    <ListItems list={theList} bind:dragState={isDragging} on:clone={cloneListItem} on:update={updateListItem} on:drop={dividerDropHandler} on:dividerDrop={dividerDropHandler} on:nest={nestHandler} on:add={addListItem} on:destroy={destroyListItem} on:dragging={setDragging} on:dragStop={unsetDragging}/>

    <ListDroppableDivider item={{ ancestry: [], index: lastIndex }} bind:dragState={isDragging} on:drop={dividerDropHandler} on:add={addListItem}/>
    {#if list.is_archived == false}
    <QuickEntry on:add={addListItemToRoot} stubPlaceholder="Add Top Level List Item" />
    {/if}

  
  </PageContent>

  
{/if}
<style lang="sass">

  h6
    text-transform: uppercase
    margin-bottom: -4px
    color: var(--gray)
    letter-spacing: 0.75px

  hr
    margin-bottom: 36px

</style>
