<script lang="coffeescript">
  export value = undefined
  # todo: show time will change conditions

  translate = (val, dummy)->
    if Object.isDate(val)
      theDate = val
    else
      theDate = Date.create(val)




    # check if past or future
    # if past - "ago"
    # if future - "from now"


    dateFormat = "%Y-%m-%d"
    timeFormat = "%H:%M"

    isFuture = theDate.isFuture()

    if isFuture
      prefix = "last"
      suffix = "from now"
    else
      prefix = "next"
      suffix = "ago"

    isPast = !isFuture

    if theDate.isToday()
      "Today"
    else if theDate.isTomorrow()
      "Tomorrow"
    else if theDate.isYesterday()
      "Yesterday"
    else if theDate.isBetween(Date.create(), Date.create().endOfWeek())
      theDate.format("%A")
    else if theDate.isBetween(Date.create().addWeeks(1).beginningOfWeek(), Date.create().addWeeks(1).endOfWeek())
      theDate.format("Next %A")
    else
      theDate.format("%a, %b {d}, %Y")


  currentDate = Date.create()

  recalc = ()->
    currentDate = Date.create()
    # console.log "recalc value #{translate(value)}"

  recalc()

  recalc.every(60 * 1000) # recalc every minute

  `$: output = translate(value, currentDate)`
</script>

<div date="{currentDate}">{output}</div>

