<script lang="coffeescript">


  import { createEventDispatcher } from "svelte"
  
  import {
    FlexColumn, 
    FlexColumnCell, 
    FlexRow, 
    FlexRowCell, 
  } from "braintapper-svelte-flex"


  import { 
    debug, 
    ButtonIcon,
    DateCalendar, 

    FormatRelativeDate,
    IconSvg
  } from "../../../Braintapper"

  dispatch = createEventDispatcher()

  export dateValue = undefined

  export isShowing = undefined

  # tempValue = dateValue

  relativeDate = undefined


  clear = ()->
    
    dispatch "tippy-event", { event: "clear", value: "" }

  setDate = (e)->
    console.error "_DueDate setDate #{e.detail}"
    dispatch "tippy-event", { event: "setDate", value: e.detail }


  dueDate = (due_date)->

    if due_date?
      if Object.isDate(due_date)
        return due_date
      else
        return Date.create(due_date)
    else
      return due_date


  `$: relativeDate = dueDate(dateValue)`

</script>




<FlexColumn>
  <FlexColumnCell>
    <FlexRow>
      <FlexRowCell flex="flex" style="padding: 4px 0px 4px 8px">
        {#if dateValue == null}
          No date assigned
        {:else}
          <FormatRelativeDate value={relativeDate}/>
        {/if}
      </FlexRowCell>
      <FlexRowCell>
        <ButtonIcon on:click={clear}>
          <IconSvg icon="clear"/>
        </ButtonIcon>
      </FlexRowCell>
    </FlexRow>
  </FlexColumnCell>
  <FlexColumnCell>
    <div calendar>
      <DateCalendar value={dateValue} bind:isShowing={isShowing} on:change={setDate}/>
    </div>
  </FlexColumnCell>
</FlexColumn>
